.description {
	margin-bottom: $section-margin_pc;
	.title_h6 {
		padding-bottom: 15px;
		border-bottom: 1px solid $border-color;
		margin-bottom: 15px;
		font-weight: bold;
	}

	@include _mobile-tablet {
		margin-bottom: 0;
	}

	@include _mobile {
		.title_h6 {
			display: none;
			opacity: 0;
			visibility: hidden;
		}
	}

	@include _tablet {
		.title_h6 {
			text-align: center;
		}
	}
}

.description-info {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	flex-wrap: wrap;
	font-weight: bold;
	font-size: 16px;
	line-height: 24px;
	letter-spacing: 0.115em;
	div {
		width: 50%;
		margin-bottom: 60px;
		&:nth-child(even) {
			text-align: right;
			display: flex;
			align-items: center;
			justify-content: flex-end;
		}
	}

	.status {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		svg {
			width: 16px;
			height: 16px;
			fill: transparent;
			margin-right: 15px;
		}
		&_violet {
			color: $violet4;
			svg {
				color: $violet4;
			}
		}
	}

	.vendore-code {
		color: $grey2;
	}

	.price {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		&__current {
			display: block;
			margin-right: 60px;
			font-weight: bold;
			font-size: 30px;
			line-height: 24px;
			letter-spacing: 0.115em;
			color: #000000;
		}
		&__old {
			display: block;
			font-weight: bold;
			font-size: 24px;
			line-height: 24px;
			letter-spacing: 0.115em;
			text-decoration: line-through;
			color: $grey1;
		}
	}

	@include _mobile-tablet {
		div {
			margin-bottom: 30px;
		}
		.price {
			width: 100%;
			justify-content: space-between;
			&__current {
				font-size: 26px;
				line-height: 1;
				margin-right: 0;
			}
			&__old {
				font-size: 20px;
				line-height: 24px;
			}
		}
		.brand {
			order: -1;
			margin-bottom: 5px;
		}
		.status {
			order: -2;
			margin-bottom: 5px;
		}
		.vendore-code {
			width: 100%;
			text-align: left;
			justify-content: flex-start !important;
		}
	}
	@include _mobile {
		font-size: 12px;
	}
	@include _tablet {
		font-size: 14px;
	}
}

.description-options {
	margin-bottom: 60px;
	&__title {
		@extend %title-5;
		margin-bottom: 15px;
		letter-spacing: 0.115em;
	}
	&__row {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		flex-wrap: wrap;
		padding: 15px 0;
		border-top: 1px solid $border-color;
		border-bottom: 1px solid $border-color;
		.owl-carousel {
			margin-right: 45px;
			width: calc(100% - 85px);
		}
		&_bottom-padding {
			padding-bottom: 0;
		}
	}
	&__radio {
		cursor: pointer;
		input {
			opacity: 0;
			visibility: hidden;
			display: none;
		}
		.checkmark {
			border: 2px solid transparent;
			width: 90px;
			height: 30px;
			display: block;
			transition: 0.3s all;
			&:hover {
				filter: brightness(0.5);
			}
			&_text {
				width: fit-content;
				min-width: 90px;
				font-weight: bold;
				font-size: 16px;
				line-height: 24px;
				letter-spacing: 0.115em;
				color: $black;
				text-align: center;
				padding: 8px 10px;
				border: 1px solid $border-color;
				height: auto;
				margin-right: 15px;
				margin-bottom: 15px;
				background-color: #fff;
				&:hover {
				}
			}
		}

		input:checked ~ .checkmark {
			border-color: $violet4;
		}
		&:last-child {
			.checkmark_text {
				margin-right: 0;
			}
		}
	}
	&__carousel-btn {
		border: none;
		background-color: transparent;
		padding: 0;
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 2;
		position: relative;
		svg {
			width: 22px;
			height: 22px;
			color: $black;
		}
	}

	@include _mobile-tablet {
		&__row {
			.owl-carousel {
				width: 100%;
			}
		}
		&__carousel-btn {
			display: none;
			opacity: 0;
			visibility: hidden;
		}
	}

	@include _mobile {
		margin-bottom: 30px;
		&__row {
			&_bottom-padding {
				justify-content: center;
			}
		}
		&__radio {
			.checkmark {
				width: 30px;
				height: 30px;
				&_text {
					height: auto;
					font-size: 14px;
					line-height: 1;
				}
			}
			&:last-child {
				.checkmark_text {
					margin-right: 15px;
				}
			}
		}
	}

	@include _tablet {
		&__radio {
			.checkmark {
				width: 60px;
			}
		}
	}
}

.description-actions {
	margin-bottom: 60px;
	.btn {
		padding: 16px 30px;
		font-size: 18px;
	}

	@include _mobile-tablet {
		margin-bottom: 45px;
		.btn {
			margin-bottom: 15px;
		}
	}

	@include _mobile {
		.btn {
			padding: 10px 30px;
		}
	}
}

.description-tabs {
	margin-bottom: 60px;
	&__heading {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-bottom: 30px;
		a {
			font-weight: bold;
			font-size: 18px;
			line-height: 24px;
			letter-spacing: 0.115em;
			color: $black;
			text-decoration: none;
			display: block;
			padding-bottom: 10px;
			margin-right: 100px;
			position: relative;
			transition: 0.3s all;
			text-shadow: $black 0.5px 0px, $black 0.5px 0.5px, $black 0px 0.5px,
				$black -0.5px 0.5px, $black -0.5px 0px, $black -0.5px -0.5px,
				$black 0px -0.5px, $black 0.5px -0.5px;
			&::after {
				content: '';
				width: 100%;
				height: 2px;
				position: absolute;
				bottom: 0;
				background-color: $violet4;
				left: 0;
				opacity: 0;
				visibility: hidden;
				transition: 0.3s all;
			}
			&:last-child {
				margin-right: 0;
			}
		}

		.active {
			color: $violet4;
			text-shadow: $violet4 0.5px 0px, $violet4 0.5px 0.5px, $violet4 0px 0.5px,
				$violet4 -0.5px 0.5px, $violet4 -0.5px 0px, $violet4 -0.5px -0.5px,
				$violet4 0px -0.5px, $violet4 0.5px -0.5px;
			&::after {
				opacity: 1;
				visibility: visible;
			}
		}
	}
	&__pane {
		opacity: 0;
		visibility: hidden;
		transition: 0.3s all;
		max-height: 0;
	}
	&__panes .active {
		max-height: 700px;
		opacity: 1;
		visibility: visible;
	}

	@include _mobile {
		margin-bottom: 10px;
		&__heading {
			a {
				font-size: 14px;
				line-height: 16px;
				text-align: center;
				margin-right: 20px;
				&:last-child {
					margin-right: 0;
				}
			}
		}
	}
}
