.seo {
	margin-bottom: $section-margin_pc;
	@for $i from 1 to 6 {
		h#{$i} {
			text-align: center;
			margin-bottom: 70px;
			@media (min-width: 768px) {
				margin-bottom: 30px;
			}
		}
	}
	//titles by class
	.title {
		@for $i from 1 to 6 {
			&_h#{$i} {
				text-align: center;
				margin-bottom: 70px;
			}
		}
	}

	p {
		@extend %seo-text;
		margin-bottom: 30px;
		&:last-child {
			margin-bottom: 0;
		}
	}

	@include _mobile {
		margin-bottom: 100px;
		@for $i from 1 to 6 {
			h#{$i} {
				margin-bottom: 30px;
			}
		}

		.title {
			@for $i from 1 to 6 {
				&_h#{$i} {
					text-align: center;
					margin-bottom: 30px;
				}
			}
		}
	}
}
