.social {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	&__item {
		width: 36px;
		height: 36px;
		border: 1px solid $border-color2;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-right: 15px;
		transition: 0.3s all;
		&:hover,
		&:focus {
			background-color: $violet4;
			svg {
				color: #fff;
			}
		}
		&:last-child {
			margin-right: 0;
		}
		svg {
			width: 18px;
			height: 18px;
		}
	}
}
