.filter {
	position: sticky;
	top: 177px;
	z-index: 5;
	background-color: #fff;
	.container {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		padding-top: 10px;
		padding-bottom: 10px;
		border-top: 1px solid $border-color;
		border-bottom: 1px solid $border-color;
		margin-bottom: 70px;
	}
	&__btn {
		border: none;
		background-color: transparent;
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: center;
		padding-right: 50px;
		border-right: 1px solid $border-color;
		svg {
			width: 20px;
			height: 20px;
			margin-right: 10px;
		}
	}
	&__item {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0 50px;
		cursor: pointer;
		border-right: 1px solid $border-color;
		position: relative;
		&_last {
			border-right: none;
		}
	}
	&__arrow {
		width: 18px;
		height: 18px;
		margin-left: 5px;
		transition: 0.3s all;
		svg {
			width: 100%;
			height: 100%;
		}
	}
	&__btn-cls {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
	@include _mobile-tablet {
		position: fixed;
		top: 0;
		z-index: 25;
		width: 100%;
		transform: translateY(-200%);
		transition: 0.3s all;
		height: 100%;
		overflow-y: auto;
		.container {
			margin-bottom: 0;
			flex-direction: column;
			align-items: flex-start;
			border: none;
		}
		&-active {
			transform: translateY(0);
		}
		&__btn {
			margin-right: auto;
			margin-left: auto;
		}
		&__btn-cls {
			opacity: 1;
			visibility: visible;
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 17px 40px;
			border: 1px solid $border-color;
			font-weight: bold;
			font-size: 16px;
			line-height: 16px;
			letter-spacing: 0.115em;
			text-transform: uppercase;
			color: $black;
			background-color: transparent;
			width: 100%;
			margin-bottom: 30px;
			cursor: pointer;
			svg {
				width: 14px;
				height: 14px;
				color: $black;
				display: flex;
				align-items: center;
				justify-content: center;
				margin-right: 15px;
				margin-bottom: 2px;
			}
		}
		&__item {
			border: none;
			align-items: flex-start;
			justify-content: flex-start;
			padding: 12.5px 0;
			flex-wrap: wrap;
		}
		.filter-menu {
			opacity: 1;
			visibility: visible;
			width: 100%;
			box-shadow: none;
			padding: 15px 0;
			&::after {
				content: none;
			}
		}
		&__arrow_active {
			transform: rotate(180deg);
		}
	}
	@include _laptop-sm {
		top: 155px;
		&__item {
			padding: 0 20px;
		}
	}
	@include _laptop {
		&__item {
			padding: 0 40px;
		}
	}

	@media (min-width: 1024px) {
		&__item {
			&:hover {
				.filter__arrow {
					transform: rotate(180deg);
				}
				.filter-menu {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}
}

.filter-menu {
	position: absolute;
	padding: 30px 37px 32px 20px;
	background-color: #fff;
	box-shadow: 0px 3px 6px rgba(79, 82, 80, 0.1);
	top: calc(100% + 14px);
	left: 0;
	opacity: 0;
	visibility: hidden;
	&::after {
		content: '';
		width: 100%;
		height: 15px;
		position: absolute;
		top: -15px;
		left: 0;
	}
	.checkbox {
		margin-bottom: 20px;
		cursor: pointer;
		&:last-child {
			margin-bottom: 0;
		}
		.checkmark {
			margin-right: 20px;
			display: block;
		}
		svg {
			width: 46px;
			height: 25px;
			margin-right: 20px;
		}
	}
	@include _mobile-tablet {
		position: relative;
		display: none;
		opacity: 0;
		visibility: visible;
	}
}
