.gallery {
	margin-bottom: 70px;
	margin-top: 70px;
	&_last {
		margin-bottom: $section-margin_pc;
	}

	@include _mobile {
		&_last {
			margin-bottom: #{$section-margin_mobile - 20px};
		}
		margin-bottom: 50px;
		img {
			margin-bottom: 20px;
		}
	}

	@include _tablet {
		&_last {
			margin-bottom: #{$section-margin_mobile - 20px};
		}
		img {
			margin-bottom: 20px;
			margin-left: auto;
			margin-right: auto;
		}
	}
}
