@for $i from 1 to 6 {
	h#{$i} {
		@extend %title-#{$i};
	}
}
//titles by class
.title {
	@for $i from 1 to 7 {
		&_h#{$i} {
			@extend %title-#{$i};
		}
	}
}

.subtitle {
	@for $i from 1 to 4 {
		&-#{$i} {
			@extend %subtitle-#{$i};
		}
	}
}

.section-title {
	@extend %title-2;
	text-align: center;
}
