.table {
	&__title {
		width: 100%;
		padding-bottom: 15px;
		border-bottom: 1px solid $border-color;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		padding-left: 15px;
		font-weight: bold;
		font-size: 14px;
		line-height: 32px;
		letter-spacing: 0.115em;
		text-transform: uppercase;
		color: $black;
		cursor: pointer;
		margin-bottom: 15px;
		svg {
			width: 30px;
			height: 30px;
			margin-right: 15px;
		}
	}
	.active {
		margin-bottom: 40px;
		opacity: 1;
		visibility: visible;
		max-height: 700px;
	}
	&_no-margin {
		.active {
			margin-bottom: 0;
		}
	}

	@include _mobile {
		.active {
			margin-bottom: 30px;
		}
	}
}
.table-item {
	// transition: 0.3s all;
	&__wrapper {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		flex-wrap: wrap;
		opacity: 0;
		visibility: hidden;
		max-height: 0;
		transition: 0.5s all;
		overflow: auto;
	}
	&__key,
	&__value {
		width: fit-content;
		font-weight: normal;
		font-size: 14px;
		line-height: 26px;
		letter-spacing: 0.115em;
		color: $black;
		white-space: nowrap;
	}

	&__value {
		text-align: right;
	}
	&__row {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		width: 100%;
	}
	&__line {
		width: 100%;
		height: 1px;
		background-color: $border-color;
		margin: 0 15px 10px 15px;
	}
	&__title {
		font-weight: bold;
		font-size: 14px;
		line-height: 14px;
		letter-spacing: 0.115em;
		text-transform: uppercase;
		color: $black;
		margin-bottom: 20px;
		text-shadow: $black 0.5px 0px, $black 0.5px 0.5px, $black 0px 0.5px,
			$black -0.5px 0.5px, $black -0.5px 0px, $black -0.5px -0.5px,
			$black 0px -0.5px, $black 0.5px -0.5px;
	}
	p {
		font-weight: normal;
		font-size: 14px;
		line-height: 22px;
		letter-spacing: 0.115em;
		color: $black;
		margin-bottom: 25px;
		margin-top: 0;
		&:last-child {
			margin-bottom: 0;
		}
	}

	@include _mobile {
		&__wrapper {
			overflow: inherit;
		}
		&__key,
		&__value {
			font-size: 12px;
		}

		&__title {
			margin-bottom: 5px;
		}

		p {
			font-size: 12px;
			margin-bottom: 15px;
		}
	}
}
