.contacts {
	margin-bottom: $section-margin_pc;
	&__map {
		width: 100%;
		height: 100%;
	}
	&__info {
		padding: 85px 30px;
		background-color: $black;
		.title_h4 {
			margin-bottom: 20px;
			color: #fff;
		}
	}
	&__wrapper {
		margin-bottom: 60px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__href {
		font-weight: normal;
		font-size: 14px;
		line-height: 18px;
		letter-spacing: 0.115em;
		color: #ffffff;
		text-decoration: none;
		margin-bottom: 25px;
		display: block;
		transition: 0.3s all;
		&:last-child {
			margin-bottom: 0;
		}
		&:hover {
			color: $violet4;
		}
	}
	.social {
		margin-bottom: 25px;
		svg {
			color: #fff;
		}
	}
	&__timetable {
		color: #fff;
		font-weight: normal;
		font-size: 14px;
		line-height: 18px;
		letter-spacing: 0.115em;
		color: #ffffff;
	}

	@include _mobile {
		margin-bottom: 100px;
		&__info {
			padding: 20px;
			margin-top: 20px;
		}
		&__map {
			height: 300px;
		}
		&__href {
			font-size: 12px;
			line-height: 1;
		}

		&__wrapper {
			margin-bottom: 20px;
		}
	}

	@include _tablet {
		margin-bottom: 100px;
		&__info {
			padding: 20px;
		}
		&__wrapper {
			margin-bottom: 20px;
		}
	}

	@include _laptop-sm {
		margin-bottom: 100px;
		&__info {
			padding: 40px 30px;
		}
		&__wrapper {
			margin-bottom: 30px;
		}
	}
}
