.team {
	margin-bottom: $section-margin_pc;
	.section-title {
		margin-bottom: 70px;
	}
	.container {
		position: relative;
	}
	.carousel__next-btn {
		top: 52%;
	}

	@include _tablet {
		.section-title {
			margin-bottom: 30px;
		}
	}
}

.team-card {
	&__img {
		height: auto;
		img {
			height: 100%;
			object-fit: cover;
		}
	}
	&__wrapper {
		padding: 35px;
		background-color: $black;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
	}
	&__name {
		font-weight: bold;
		font-size: 24px;
		line-height: 24px;
		text-align: center;
		margin-bottom: 20px;

		color: #ffffff;
	}
	&__caption {
		font-weight: normal;
		font-size: 16px;
		line-height: 24px;
		text-align: center;
		color: $grey2;
		margin-bottom: 35px;
	}
	&__description {
		font-weight: normal;
		margin-bottom: 35px;
		font-size: 16px;
		line-height: 24px;
		text-align: center;
		color: #ffffff;
	}
	.btn {
		padding: 14px 52px;
	}

	@include _mobile {
	}

	@include _tablet {
		&__wrapper {
			padding: 20px;
		}
		&__name {
			margin-bottom: 10px;
		}
		&__caption {
			margin-bottom: 20px;
		}
	}
}
