.cart-products {
	border-bottom: 1px solid $border-color;
	margin-bottom: $section-margin_pc;
	&__heading {
		padding: 20px 0 16px;
		border-top: 1px solid $border-color;
		border-bottom: 1px solid $border-color;
		margin-bottom: 40px;
		font-weight: normal;
		font-size: 14px;
		line-height: 18px;
		letter-spacing: 0.115em;
		color: $grey2;
		svg {
			width: 20px;
			height: 20px;
		}
	}

	&__card {
		margin-bottom: 40px;
	}
	&__img {
		margin-left: auto;
		margin-right: auto;
		width: 90px;
		height: 90px;
		display: flex;
		align-items: center;
		justify-content: center;
		text-decoration: none;
		img {
			transition: 0.3s all;
		}
		&:hover {
			img {
				transform: scale(1.1);
			}
		}
	}
	&__title {
		font-weight: normal;
		font-size: 14px;
		line-height: 24px;
		letter-spacing: 0.115em;
		text-transform: uppercase;
		color: $black;
		text-decoration: none;
		transition: 0.3s all;
		position: relative;
		padding-bottom: 5px;
		&::after {
			content: '';
			width: 0;
			height: 1px;
			background-color: $violet4;
			position: absolute;
			bottom: 0;
			opacity: 0;
			visibility: hidden;
			left: 0;
			transition: 0.3s all;
		}
		&:hover {
			color: $violet4;
			&::after {
				width: 100%;
				opacity: 1;
				visibility: visible;
			}
		}
	}
	&__price {
		font-weight: bold;
		font-size: 20px;
		line-height: 24px;
		text-align: center;
		letter-spacing: 0.115em;
		color: #000000;
	}
	&__delete {
		border: none;
		background-color: transparent;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 20px;
		height: 20px;
		padding: 0;
		cursor: pointer;
		margin-left: auto;
		margin-right: auto;
		svg {
			width: 20px;
			height: 20px;
			color: $grey2;
			transition: 0.3s all;
		}
		&:hover,
		&:focus {
			svg {
				color: $violet4;
			}
		}
	}
	&__caption {
		display: none;
		opacity: 0;
		visibility: hidden;
	}

	@include _mobile {
		margin-bottom: 50px;
		&__card {
			position: relative;
		}
		&__heading {
			display: none;
			opacity: 0;
			visibility: hidden;
		}
		&__caption {
			margin: 10px 0;
			opacity: 1;
			visibility: visible;
			display: block;
			text-align: center;
			font-size: 12px;
			line-height: 1;
		}
		&__delete {
			position: absolute;
			top: -10px;
			right: 5px;
		}
		&__title {
			width: 100%;
			text-align: center;
			font-size: 14px;
			line-height: 1;
			display: flex;
			align-items: center;
			justify-content: center;
			height: 100%;
		}
	}

	@include _tablet {
		margin-bottom: 70px;
	}

	@include _laptop-sm {
		margin-bottom: 70px;
		&__title {
			line-height: 14px;
		}
	}
}
