.btn-chat {
	width: 70px;
	height: 70px;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	border: none;
	background-color: $violet4;
	cursor: pointer;
	margin-top: 20px;
	transition: 0.3s all;
	z-index: 2;
	animation: pulse 3s infinite ease-in-out;
	@keyframes pulse {
		0% {
			box-shadow: 0px 0px 0px 0px rgba(2, 100, 102, 0.2);
		}
		50% {
			box-shadow: 0px 0px 10px 30px rgba(2, 100, 102, 0.2);
		}
		100% {
			box-shadow: 0px 0px 10px 40px rgba(2, 100, 102, 0);
		}
	}
	svg {
		width: 28px;
		height: 28px;
		color: #fff;
		pointer-events: none;
	}
	.icon_1 {
		width: 28px;
		height: 28px;
		opacity: 1;
		visibility: visible;
		transition: 0.3s all;
	}
	.icon_2 {
		width: 0;
		height: 0;
		opacity: 0;
		visibility: hidden;
		transition: 0.3s all;
	}

	@include _mobile {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
}

.contact-us {
	position: fixed;
	bottom: 3%;
	right: 3%;
	display: flex;
	z-index: 20;
	flex-direction: column;
	justify-content: flex-end;

	&__wrapper {
		// transform: translateY(150%);
		opacity: 0;
		visibility: hidden;
		transition: 0.6s all;
		pointer-events: none;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		max-height: 0;
		&_active {
			// transform: translateY(0);
			opacity: 1;
			visibility: visible;
			pointer-events: inherit;
			max-height: 900px;
		}
	}

	.active {
		background-color: $violet2;
		animation: none;
		.icon_1 {
			opacity: 0;
			visibility: hidden;
			width: 0;
			height: 0;
		}
		.icon_2 {
			opacity: 1;
			visibility: visible;
			width: 28px;
			height: 28px;
		}
	}
	&__btn {
		display: flex;
		align-items: center;
		justify-content: center;
		text-decoration: none;
		width: 50px;
		height: 50px;
		border-radius: 50%;
		position: relative;
		margin-bottom: 20px;
		transition: 0.3s all;
		z-index: 1;
		&::after {
			content: '';
			width: 56px;
			height: 56px;
			position: absolute;
			z-index: -1;
			border-radius: 50%;
		}
		svg {
			width: 24px;
			height: 24px;
		}
		&_blue {
			background: linear-gradient(180deg, #3ca4f4 0%, #3189cc 100%);
			&::after {
				background: linear-gradient(
					180deg,
					rgba(60, 164, 244, 0.2) 0%,
					rgba(49, 137, 204, 0.2) 100%
				);
			}
		}
		&_blue-2 {
			background: linear-gradient(180deg, #00bdff 0%, #006fff 100%);
			&::after {
				background: linear-gradient(
					180deg,
					rgba(0, 189, 255, 0.2) 0%,
					rgba(0, 111, 255, 0.2) 100%
				);
			}
		}
		&_red {
			background: linear-gradient(
				43.94deg,
				#eea054 16.16%,
				#d64763 50.11%,
				#b62a99 83.68%
			);
			&::after {
				background: linear-gradient(
					43.94deg,
					rgba(238, 160, 84, 0.2) 16.16%,
					rgba(214, 71, 99, 0.2) 50.11%,
					rgba(182, 42, 153, 0.2) 83.68%
				);
			}
		}
		&_violet-2 {
			background: linear-gradient(180deg, #923fca 0%, #6e1aa7 100%);
			&::after {
				background: linear-gradient(
					180deg,
					rgba(146, 63, 202, 0.2) 0%,
					rgba(110, 26, 167, 0.2) 100%
				);
			}
		}
		&_green {
			background: linear-gradient(180deg, #48dc63 0%, #27b440 100%);
			&::after {
				background: linear-gradient(
					180deg,
					rgba(72, 220, 99, 0.2) 0%,
					rgba(39, 180, 64, 0.2) 100%
				);
			}
		}
		&_orange {
			background: linear-gradient(180deg, #f4953c 11.98%, #cd7626 100%);
			&::after {
				background: linear-gradient(
					180deg,
					rgba(244, 149, 60, 0.2) 11.98%,
					rgba(205, 118, 38, 0.2) 100%
				);
			}
		}
		&_violet {
			background-color: $violet4;
			&::after {
				background-color: rgba(153, 16, 155, 0.2);
			}
		}

		&:hover {
			filter: brightness(1.2);
			.tooltip {
				opacity: 1;
				visibility: visible;
				color: $black;
			}
		}
	}
	.tooltip {
		position: absolute;
		right: calc(100% + 15px);
		font-size: 12px;
		font-weight: bold;
		padding: 5px;
		border-radius: 4px;
		background-color: $border-color;
		opacity: 0;
		visibility: hidden;
		transition: 0.3s all;
		&::after {
			position: absolute;
			content: '';
			width: 0;
			height: 0;
			border-style: solid;
			left: 100%;
			top: 50%;
			transform: translateY(-50%);
			border-width: 5px 0 5px 5px;
			border-color: transparent transparent transparent #e5e5e5;
			line-height: 0px;
		}
	}

	@include _mobile-tablet {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
	@media (min-width: 1024px) and (max-width: 1900px) {
		bottom: 6%;
	}
}
