.header {
	padding-bottom: 40px;
	position: sticky;
	top: 0;
	z-index: 6;
	background-color: #fff;
	&__phone {
		display: block;
		width: fit-content;
		font-weight: normal;
		font-size: 18px;
		line-height: 20px;
		color: $black;
		text-decoration: none;
		margin-bottom: 5px;
		margin-left: 18px;
		transition: 0.3s all;
		position: relative;
		overflow: hidden;
		&::after {
			content: '';
			width: 100%;
			height: 100%;
			background-color: $black;
			z-index: -1;
			position: absolute;
			left: 0;
			transform: translateX(-100%);
			transition: 0.3s all;
			opacity: 0;
			visibility: hidden;
		}
		&:hover,
		&:focus {
			color: #fff;
			&::after {
				transform: translateX(0);
				opacity: 1;
				visibility: visible;
			}
		}
	}
	.btn-secondary {
		width: fit-content;
		padding: 8px 20px;
		font-weight: bold;
		line-height: 16px;
		margin-left: 18px;
	}
	&__top {
		padding: 30px 0;
	}
	&__logo {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 317px;
		height: 55px;
		margin-left: auto;
		margin-right: auto;
		svg {
			width: 100%;
			height: 100%;
			color: $black;
			transition: 0.3s all;
		}
		&:hover,
		&:focus {
			svg {
				transform: scale(1.05);
			}
		}
	}
	.social {
		&__item {
			svg {
				color: $black;
			}
		}
	}
	@include _mobile-tablet {
		padding-bottom: 0;
		z-index: 25;
		&__logo {
			width: 115px;
			height: 20px;
		}
		.js-header-actions {
			transition: 0.3s all;
		}
		&__top {
			padding: 15px 0;
		}
		&__phone {
			width: 50%;
			margin: 0;
			font-size: 16px;
			line-height: 18px;
			text-align: center;
		}
	}
	@include _laptop-sm {
		padding-bottom: 20px;
		&__logo {
			width: 240px;
			height: 35px;
		}

		&__phone,
		.btn-secondary {
			margin-left: auto;
			margin-right: auto;
		}
		&__phone {
			font-size: 16px;
			line-height: 18px;
		}
	}
	@include _laptop {
		&__logo {
			width: 260px;
			height: 40px;
		}
	}
}

.header-actions {
	display: flex;
	align-items: center;
	justify-content: space-between;
	a {
		width: 22px;
		height: 22px;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		color: $black;
		svg {
			width: 100%;
			height: 100%;
			transition: 0.3s all;
			color: $black;
		}
		span {
			font-weight: bold;
			font-size: 7px;
			line-height: 1;
			width: 12px;
			height: 12px;
			border-radius: 50%;
			background-color: $violet4;
			letter-spacing: 0.115em;
			color: #ffffff;
			position: absolute;
			display: flex;
			align-items: center;
			justify-content: center;
			text-align: center;
			bottom: -3px;
			right: 0;
		}
		&:hover,
		&:focus {
			svg {
				color: $violet4;
			}
		}
	}

	@include _mobile {
		a {
			opacity: 0;
			visibility: hidden;
		}
		a:last-child {
			opacity: 1;
			visibility: visible;
		}

		&_mobile {
			a {
				width: 18px;
				height: 18px;
				opacity: 1;
				visibility: visible;
			}
		}
	}
}
.header-lang {
	font-weight: 300;
	font-size: 16px;
	line-height: 20px;
	letter-spacing: 0.115em;
	a {
		text-decoration: none;
		color: $black;
		position: relative;
		transition: 0.3s all;
		overflow: hidden;
		display: inline-block;
		&::after {
			content: '';
			width: 100%;
			height: 100%;
			position: absolute;
			z-index: -1;
			background-color: $black;
			left: 0;
			top: 0;
			transform: translateX(-100%);
			opacity: 0;
			visibility: hidden;
			transition: 0.3s all;
		}
		&:hover,
		&:focus {
			color: #fff;
			&::after {
				transform: translateX(0);
				opacity: 1;
				visibility: visible;
			}
		}
	}
	.active {
		font-weight: bold;
		&:after {
			opacity: 0;
			visibility: hidden;
		}
	}

	@include _mobile-tablet {
		opacity: 0;
		visibility: hidden;
		height: 0;

		&_mobile {
			opacity: 1;
			visibility: visible;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: flex-end;
			a {
				font-weight: 300;
				font-size: 12px;
				line-height: 12px;
			}
		}
	}
}

.header-mobile {
	&__btn {
		opacity: 0;
		visibility: hidden;
		display: none;
	}
	&__wrapper {
		opacity: 0;
		visibility: hidden;
		display: none;
	}
	.js-logo {
		transition: 0.3s all;
	}
	@include _mobile-tablet {
		&__btn,
		&__btn-close {
			opacity: 1;
			visibility: visible;
			display: block;
			cursor: pointer;
			position: relative;
			width: fit-content;
			width: 18px;
			height: 18px;
			padding: 0;
			background-color: transparent;
			border: none;
			span {
				position: relative;
				display: block;
				width: 18px;
				height: 2px;
				background-color: $black;
				border-radius: 2px;
				margin-bottom: 4px;
				transition: 0.3s all;
				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		&__btn-close {
			span {
				position: absolute;
				transform-origin: center;
				margin-bottom: 0;
				top: 8px;
				&:nth-child(1) {
					transform: rotate(45deg);
				}
				&:nth-child(2) {
					transform: rotate(-45deg);
				}
			}
		}
		&__wrapper {
			opacity: 1;
			visibility: visible;
			display: flex;
			align-items: flex-start;
			justify-content: flex-start;
			align-content: flex-start;
			flex-wrap: wrap;
			position: fixed;
			height: 100%;
			top: 0;
			left: 0;
			width: 100%;
			background-color: #fff;
			padding: 15px 8px 30px 8px;
			transform: translateY(-200%);
			transition: 0.6s all;
			overflow-y: auto;
			&_active {
				transform: translateY(0);
			}
		}
		&__heading {
			padding-bottom: 15px;
			border-bottom: 1px solid $border-color;
			margin-bottom: 30px;
			width: 100%;
		}
		&__menu {
			width: 100%;
			display: flex;
			align-items: flex-start;
			justify-content: flex-start;
			flex-wrap: wrap;
		}
		&__bottom {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: flex-start;
			flex-wrap: wrap;
			.social {
				width: 50%;
			}

			.btn-secondary {
				margin-top: 20px;
				margin-left: auto;
				margin-right: auto;
				padding: 10px 75px;
			}
		}
	}

	@include _tablet {
		&__wrapper {
			padding: 15px 15px 30px 15px;
		}
	}
}
