.category-card {
	background-repeat: no-repeat;
	background-position: center center;
	background-size: 100% 100%;
	background-repeat: no-repeat;
	flex-direction: column;
	width: 100%;
	padding: 30px;
	display: flex;
	align-items: flex-start;
	margin-bottom: 30px;
	text-decoration: none;
	transition: 0.3s all;
	&:last-child {
		margin-bottom: 0;
	}
	&_sm {
		min-height: 280px;
	}
	&_lg {
		min-height: 340px;
	}
	&_text-bottom {
		justify-content: flex-end;
	}
	&_text-top {
		justify-content: flex-start;
	}
	&__title {
		font-weight: bold;
		font-size: 32px;
		line-height: 41px;
		letter-spacing: 0.115em;
		text-transform: uppercase;
		margin-bottom: 20px;
		text-shadow: $black 0.5px 0px, $black 0.5px 0.5px, $black 0px 0.5px,
			$black -0.5px 0.5px, $black -0.5px 0px, $black -0.5px -0.5px,
			$black 0px -0.5px, $black 0.5px -0.5px;
		&_black {
			color: $black;
		}
		&_white {
			color: #fff;
		}
	}
	.btn {
		padding: 8.5px 33px;
	}

	@media (min-width: 320px) and (max-width: 380px) {
		background-position: -220px;
	}

	@include _mobile {
		padding: 20px;
		background-size: cover;
		margin-bottom: 20px;

		width: 100%;
		margin-left: auto;
		margin-right: auto;
		&:last-child {
			margin-bottom: 20px;
		}
		&__title {
			font-size: 18px;
			line-height: 23px;
		}
		.btn {
			padding: 6.5px 31px;
		}
		&_sm {
			min-height: 260px;
		}
	}

	@include _tablet {
		background-size: cover;
		&__title {
			font-size: 24px;
			line-height: 30px;
		}

		&_lg {
			min-height: 280px;
		}
		&_sm {
			min-height: 200px;
		}
	}

	@include _laptop-sm {
		&_lg {
			min-height: 300px;
		}
		&_sm {
			min-height: 220px;
		}

		&__title {
			font-size: 28px;
			line-height: 36px;
		}
	}

	@media (min-width: 1024px) {
		&:hover,
		&:focus {
			background-size: 120% 120%;
		}
	}
}
