.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	background-color: transparent;
	text-decoration: none;
	border: none;
	transition: 0.3s all;

	&-primary {
		@extend %btn-text1;
	}
	&-secondary {
		@extend %btn-text2;
	}
	&-border-pink {
		border: 1px solid $violet4;
		border-radius: 20px;
		color: $violet4;

		&:hover,
		&:focus {
			background-color: $violet4;
			color: #fff;
		}
	}
	&-violet-1 {
		color: #fff;
		background-color: $violet4;
		&:hover,
		&:focus,
		&:disabled {
			background-color: $violet2;
		}
		&:disabled {
			cursor: default;
		}
	}
	&-pink {
		background-color: rgba(153, 16, 155, 0.2);
		transition: 0.3s all;
		color: $black;
		&:hover,
		&:focus {
			background-color: $violet7;
			color: #fff;
		}
	}
	&-border {
		position: relative;
		overflow: hidden;
		z-index: 1;
		&::after {
			content: '';
			position: absolute;
			top: -1px;
			left: 1px;
			width: 100%;
			height: 100%;
			transform: translateX(-150%);
			transition: 0.3s all;
			z-index: -1;
		}
		&_white {
			border: 1.5px solid #ffffff;
			color: #fff;
			&::after {
				background-color: #fff;
			}
			&:hover,
			&:focus {
				color: $black;
			}
		}
		&_black {
			color: $black;
			border: 1.5px solid $black;
			&::after {
				content: '';
				background-color: $black;
			}
			&:hover,
			&:focus {
				color: #fff;
			}
		}
		&:hover,
		&:focus {
			&::after {
				transform: translateX(0);
			}
		}
	}
	&-white {
		background-color: #fff;
		color: $black;
	}
	&-size-1 {
		padding: 10px 33px;
		width: fit-content;
	}
	&-arrow {
		padding: 8px 86px 8px 34px;
		background-color: $violet4;
		width: fit-content;
		svg {
			width: 20px;
			height: 20px;
			color: #fff;
			transition: 0.3s all;
		}
		&:hover,
		&:focus {
			svg {
				transform: translateX(5px);
			}
		}
	}
}

.btn-like {
	width: 48px;
	height: 48px;
	border: none;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: transparent;
	cursor: pointer;
	svg {
		width: 22px;
		height: 22px;
	}
}

.btn-carousel {
	border: none;
	background-color: transparent;
	cursor: pointer;
	padding: 0;
	width: 28px;
	height: 28px;
	svg {
		width: 100%;
		height: 100%;
		transition: 0.3s all;
	}

	&:hover,
	&:focus {
		svg {
			transform: scale(1.1);
		}
	}

	&_prev {
		svg {
			transform: rotate(180deg);
		}
		&:hover,
		&:focus {
			svg {
				transform: rotate(180deg) scale(1.2);
			}
		}
	}
}

.btn-load-more {
	font-weight: normal;
	font-size: 14px;
	line-height: 18px;
	text-align: center;
	letter-spacing: 0.115em;
	color: $black;
	background-color: transparent;
	cursor: pointer;
	border: none;
	margin-left: auto;
	margin-right: auto;
	display: flex;
	align-items: center;
	justify-content: center;
	max-width: 143px;
	svg {
		margin-left: 10px;
		width: 22px;
		height: 22px;
		color: $violet4;
		transform: rotate(90deg);
		transition: 0.3s all;
	}
	&:hover,
	&:focus {
		svg {
			transform: rotate(90deg) translateX(5px);
		}
	}
}
