.instagram {
	margin-bottom: $section-margin_pc;
	.container {
		position: relative;
	}
	.section-title {
		margin-bottom: 30px;
	}
	.subtitle-1 {
		text-transform: none;
		margin-bottom: 70px;
		text-align: center;
	}
	&__item {
		display: flex;
		align-items: center;
		justify-content: center;
	}
	&__next-btn {
		position: absolute;
		padding: 8px 50px 8px 20px;
		background-color: $violet4;
		border: none;
		cursor: pointer;
		top: 65%;
		z-index: 2;
		right: -50px;
		svg {
			color: #fff;
			width: 20px;
			height: 20px;
			transition: 0.3s all;
		}
		&:hover {
			svg {
				transform: translateX(5px);
			}
		}
	}

	@include _mobile-tablet {
		&__next-btn {
			display: none;
			opacity: 0;
			visibility: hidden;
		}
	}
	@include _mobile {
		margin-bottom: 100px;
		.subtitle-1 {
			margin-bottom: 60px;
			font-size: 18px;
			line-height: 23px;
		}
	}
	@include _tablet {
	}
	@media (min-width: 1024px) and (max-width: 1600px) {
		&__next-btn {
			right: 0;
		}
	}
}
