.options {
	&__heading {
		padding-bottom: 15px;
		margin-bottom: 15px;
		border-bottom: 1px solid $border-color;
		font-weight: bold;
		font-size: 16px;
		line-height: 24px;
		letter-spacing: 0.115em;
		color: $black;
	}
	&__wrapper {
		padding-bottom: 15px;
		margin-bottom: 20px;
		border-bottom: 1px solid $border-color;
		&_no-border {
			border-bottom: none;
			margin-bottom: 60px;
		}
		span {
			font-size: 20px;
			line-height: 20px;
			letter-spacing: 0.115em;
			color: $black;
		}
	}
	&__row {
		margin-bottom: 30px;
	}
	&__title {
		font-weight: bold;
		font-size: 18px;
		line-height: 24px;
		letter-spacing: 0.115em;
		color: $black;
		margin-bottom: 25px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		svg {
			width: 24px;
			height: 24px;
			color: $black;
			margin-right: 15px;
		}
	}
	.checkbox {
		width: fit-content;
		.checkmark {
			margin-right: 15px;
		}
	}

	@include _mobile {
		&__heading {
			font-size: 14px;
			line-height: 24px;
			text-align: center;
		}
		&__wrapper {
			&_no-border {
				margin-bottom: 30px;
			}
			.js-select {
				margin-bottom: 10px;
			}
			span {
				font-size: 16px;
				line-height: 20px;
			}
		}
	}
}
