.modal {
	background-color: #fff;
	padding: 61px 45px;
	display: none;
	min-width: 445px;
	.title_h2 {
		margin-bottom: 30px;
		text-align: center;
	}
	&__subtitle {
		font-weight: bold;
		font-size: 14px;
		line-height: 20px;
		text-align: center;
		letter-spacing: 0.115em;
		text-transform: uppercase;
		color: $black;
	}

	.input {
		margin-bottom: 20px;
	}

	.btn {
		width: 100%;
		padding: 10px 20px;
	}

	&__row {
		max-width: 460px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
	}
	&__img {
		margin-right: 15px;
		min-width: 89px;
		min-height: 89px;
	}
	&__text {
		font-weight: normal;
		font-size: 14px;
		line-height: 24px;
		letter-spacing: 0.115em;
		text-transform: uppercase;
		color: $black;
		a {
			color: $violet4;
		}
	}

	@include _mobile {
		min-width: auto;
		padding: 30px 20px;
		&__row {
			flex-direction: column;
		}
		&__img {
			margin-bottom: 20px;
		}

		.title_h2 {
			font-size: 20px;
		}
		.input {
			input {
				padding: 11px 16px;
			}
		}
	}
}

.modal-cart {
	padding: 50px 47px;
	@include _mobile {
		padding: 30px 20px;
	}
}
.fancybox-bg {
	background: rgba(79, 82, 80, 0.7);
	backdrop-filter: blur(7px);
}
