.request {
	margin-bottom: 100px;
	&__img {
		&_gradient {
			position: relative;
			&::after {
				content: '';
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				background: linear-gradient(
					0deg,
					#ffffff 0%,
					rgba(255, 255, 255, 0) 14.45%
				);
			}
		}
	}
	.title_h2 {
		margin-bottom: 25px;
	}
	.subtitle-2 {
		text-transform: none;
		font-weight: normal;
		text-align: left;
		margin-bottom: 50px;
	}
	.btn-primary {
		padding: 14px 52px;
		width: fit-content;
		font-weight: normal;
		font-size: 18px;
		line-height: 28px;
	}
	&_no-margin {
		margin-bottom: 0;
	}

	@include _mobile-tablet {
		&__img {
			width: fit-content;
			margin-left: auto;
			margin-right: auto;
		}
		.subtitle-2 {
			text-align: center;
			margin-bottom: 30px;
		}
		.title_h2 {
			font-size: 20px;
		}
		.btn-primary {
			margin-left: auto;
			margin-right: auto;
		}
	}
	@include _mobile {
		.btn-primary {
			padding: 10px 20px;
		}
	}
}
