// grid

$grid-columns: 12;
$gutter-width: 30px;
$outer-margin: 30px;
$gutter-compensation: ($gutter-width * 0.5) * -1;
$breakpoints: sm 768px, md 1024px, lg 1430px;

.container {
	box-sizing: border-box;
	max-width: 1430px;
	margin: 0 auto;
	width: 100%;
	padding-right: $outer-margin / 2;
	padding-left: $outer-margin / 2;
	@include _mobile {
		padding-right: 8px;
		padding-left: 8px;
	}
}

.container-fluid {
	margin-right: auto;
	margin-left: auto;
	padding-right: $outer-margin;
	padding-left: $outer-margin;
}

.row {
	box-sizing: border-box;
	display: flex;
	flex: 0 1 auto;
	flex-direction: row;
	flex-wrap: wrap;
	margin-right: $gutter-compensation;
	margin-left: $gutter-compensation;
	@include _mobile {
		margin-right: -8px;
		margin-left: -8px;
	}
}

.row.reverse {
	flex-direction: row-reverse;
}
.reverse {
	flex-direction: row-reverse;
	&_row {
		flex-direction: row-reverse;
	}
	&_col {
		flex-direction: column-reverse;
	}
}

.col.reverse {
	flex-direction: column-reverse;
}

$name: xs;
[class*='col-#{$name}'] {
	box-sizing: border-box;
	flex: 0 0 auto;
	padding-right: $gutter-compensation * -1;
	padding-left: $gutter-compensation * -1;
}

@for $i from 1 through $grid-columns {
	.col-#{$name}-#{$i} {
		flex-basis: 100% / $grid-columns * $i;
		max-width: 100% / $grid-columns * $i;
	}
}

@for $i from 1 through $grid-columns {
	.col-#{$name}-offset-#{$i} {
		margin-left: 100% / $grid-columns * $i;
	}
}

.col-#{$name} {
	flex-grow: 1;
	flex-basis: 0;
	max-width: 100%;
}

.start-#{$name} {
	justify-content: flex-start;
	text-align: start;
}

.center-#{$name} {
	justify-content: center;
	text-align: center;
}

.end-#{$name} {
	justify-content: flex-end;
	text-align: end;
}

.top-#{$name} {
	align-items: flex-start;
}

.middle-#{$name} {
	align-items: center;
}

.bottom-#{$name} {
	align-items: flex-end;
}

.around-#{$name} {
	justify-content: space-around;
}

.between-#{$name} {
	justify-content: space-between;
}

.first-#{$name} {
	order: -1;
}

.last-#{$name} {
	order: 1;
}

.visible-#{$name} {
	display: block;
}
.hidden-#{$name} {
	display: none;
}

@each $breakpoint in $breakpoints {
	$name: nth($breakpoint, 1);
	$size: nth($breakpoint, 2);
	@media only screen and (min-width: $size) {
		[class*='col-#{$name}'] {
			box-sizing: border-box;
			flex: 0 0 auto;
			padding-right: $gutter-compensation * -1;
			padding-left: $gutter-compensation * -1;
		}
		@for $i from 1 through $grid-columns {
			.col-#{$name}-#{$i} {
				flex-basis: 100% / $grid-columns * $i;
				max-width: 100% / $grid-columns * $i;
			}
		}
		@for $i from 1 through $grid-columns {
			.col-#{$name}-offset-#{$i} {
				margin-left: 100% / $grid-columns * $i;
			}
		}
		.col-#{$name} {
			flex-grow: 1;
			flex-basis: 0;
			max-width: 100%;
		}
		.start-#{$name} {
			justify-content: flex-start;
			text-align: start;
		}

		.visible-#{$name} {
			display: block;
		}
		.hidden-#{$name} {
			display: none;
		}

		.center-#{$name} {
			justify-content: center;
			text-align: center;
		}

		.end-#{$name} {
			justify-content: flex-end;
			text-align: end;
		}

		.top-#{$name} {
			align-items: flex-start;
		}

		.middle-#{$name} {
			align-items: center;
		}

		.bottom-#{$name} {
			align-items: flex-end;
		}

		.around-#{$name} {
			justify-content: space-around;
		}

		.between-#{$name} {
			justify-content: space-between;
		}

		.first-#{$name} {
			order: -1;
		}

		.last-#{$name} {
			order: 1;
		}
	}
}

@include _mobile {
	.col-xs-12,
	.col-xs-11,
	.col-xs-10,
	.col-xs-9,
	.col-xs-8,
	.col-xs-7,
	.col-xs-6,
	.col-xs-5,
	.col-xs-4,
	.col-xs-3,
	.col-xs-2,
	.col-xs-1,
	.col-xs {
		padding-left: 8px;
		padding-right: 8px;
	}
}
