.wishlist {
	margin-bottom: $section-margin_pc;
	&__clear {
		width: fit-content;
		margin-left: auto;
		display: block;
		background-color: transparent;
		border: none;
		cursor: pointer;
		font-weight: normal;
		font-size: 14px;
		line-height: 16px;
		color: $black;
		text-decoration: underline;
		margin-bottom: 40px;
		&:hover {
			text-decoration: none;
		}
	}

	@include _mobile {
		&__clear {
			margin-bottom: 20px;
		}
	}

	@include _mobile-tablet {
		.product-card {
			margin-left: auto;
			margin-right: auto;
		}
	}
}
