.blog-section {
	position: relative;
	z-index: 1;
	margin-bottom: #{$section-margin_pc + 55px};
	.section-title {
		margin-bottom: #{$section-title-margin_pc + 40px};
	}
	&__decoration-img {
		position: absolute;
		left: 0;
		top: 110px;
		z-index: -1;
		width: 50%;
		height: 540px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	@include _mobile-tablet {
		margin-bottom: 100px;

		&__decoration-img {
			display: none;
			opacity: 0;
			visibility: hidden;
		}
	}
	@include _tablet {
		margin-bottom: 100px;
	}
}

.blog-section-item {
	padding: 66px 30px 69px 30px;
	background-color: $black;
	max-width: 600px;
	position: relative;
	display: block;
	text-decoration: none;
	margin-left: auto;
	&__date {
		font-weight: bold;
		font-size: 16px;
		line-height: 1;
		color: $black;
		background-color: #fff;
		padding: 13px 18px;
		width: fit-content;
		position: absolute;
		left: 0;
		top: -10px;
	}
	&__title {
		font-weight: bold;
		font-size: 20px;
		line-height: 26px;
		text-transform: uppercase;
		color: #ffffff;
		margin-bottom: 20px;
	}
	&__text {
		p {
			margin-bottom: 30px;
			color: #fff;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	.btn-arrow {
		position: absolute;
		bottom: 0;
		right: -14px;
	}
	@include _mobile {
		padding: 65px 30px 40px 12px;

		&__title {
			font-size: 16px;
			line-height: 18px;
		}
	}
	@include _mobile-tablet {
		max-width: none;
		margin-bottom: 20px;
		margin-right: 0;
		.btn-arrow {
			opacity: 0;
			visibility: hidden;
			display: none;
		}
	}
}

.blog-section-preview {
	min-height: 200px;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: 100% 100%;
	position: relative;
	max-width: 430px;
	margin-bottom: 30px;
	margin-left: auto;
	font-weight: bold;
	font-size: 16px;
	line-height: 18px;
	color: #ffffff;
	text-transform: uppercase;
	z-index: 1;
	padding: 20px 30px;
	display: flex;
	align-items: flex-end;
	justify-content: flex-start;
	text-decoration: none;
	transition: 0.3s all;
	&::after {
		content: '';
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: rgba(79, 82, 80, 0.6);
		top: 0;
		left: 0;
		z-index: -1;
	}
	&:last-child {
		margin-bottom: 0;
	}
	&__date {
		position: absolute;
		top: 0;
		left: 0;
		padding: 7px 11px 7px 11px;
		max-width: 42px;
		background-color: $black;
	}
	&__title {
		font-weight: bold;
	}
	&:first-child & {
		&__date {
			padding-top: 47px;
			top: -40px;
		}
	}

	&:hover {
		background-size: 120% 120%;
	}
	@include _mobile {
		padding: 20px;
		background-size: cover;
		max-width: none;
		&:first-child & {
			&__date {
				padding-top: 7px;
				top: 0;
			}
		}

		&__title {
			font-size: 14px;
			line-height: 28px;
		}
	}

	@include _tablet {
		display: inline-flex;
		width: calc(50% - 8px);
		max-width: none;
		margin-right: 10px;
		margin-bottom: 0;
		&:last-child {
			margin-right: 0;
		}
		&:first-child & {
			&__date {
				padding-top: 7px;
				top: 0;
			}
		}
	}
}
