.quantity {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	max-width: 63px;
	width: 100%;
	margin-left: auto;
	margin-right: auto;
	input {
		font-weight: normal;
		width: 30px;
		font-size: 14px;
		line-height: 14px;
		letter-spacing: 0.115em;
		text-transform: uppercase;
		color: $black;
		padding: 2px;
		border: none;
		text-align: center;
		background-color: transparent;
	}
}
.decrement,
.increment {
	background-color: transparent;
	border: none;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0;
	width: 15px;
	min-width: 15px;
	height: 15px;
	cursor: pointer;
	svg {
		width: 100%;
		height: 100%;
		color: $grey2;
		transition: 0.3s all;
	}
	&:hover,
	&:focus {
		svg {
			color: $violet4;
		}
	}
}
