.mission {
	margin-bottom: $section-margin_pc;

	&__wrapper {
		background-repeat: no-repeat;
		background-position: center center;
		background-image: url(../img/pattern.png);
		background-size: 100% 100%;
		background-color: $black;
		padding: 80px 53px 80px 40px;
	}

	.ul-check {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		flex-wrap: wrap;
		li {
			max-width: 300px;
			color: #fff;
			margin-bottom: 30px;
			font-weight: bold;
			font-size: 14px;
			line-height: 20px;
		}
	}

	.subtitle-3 {
		margin-bottom: 20px;
	}

	.title_h4 {
		color: #fff;
		margin-bottom: 50px;
	}

	@include _mobile-tablet {
		&__img {
			width: fit-content;
			margin-left: auto;
			margin-right: auto;
		}
		.title_h4 {
			margin-bottom: 20px;
		}
	}

	@include _mobile {
		&__wrapper {
			padding: 20px;
		}
	}

	@include _tablet {
		&__wrapper {
			padding: 30px;
		}
		.title_h4 {
			margin-bottom: 30px;
		}
	}

	@include _laptop-sm {
		&__wrapper {
			padding: 60px 40px;
		}
	}
}
