.rating-wrapper {
	display: flex;
	align-items: center;
	justify-content: flex-start;
}
.rating {
	border: none;
	float: left;
	padding: 0;
	margin: 0;
	input {
		cursor: pointer;
		display: none;
	}
	&__caption {
		font-weight: bold;
		font-size: 16px;
		line-height: 24px;
		letter-spacing: 0.115em;
		color: $grey2;
		margin-right: 35px;
	}
	label {
		float: right;
		width: 27px;
		height: 27px;
		cursor: pointer;
		display: inline-block !important;
		cursor: pointer;
		margin-right: 10px;
		svg {
			width: 100%;
			height: 100%;
			stroke: #9e9e9e;
			color: transparent;
			transition: 0.3s all;
		}
		&:first-of-type {
			margin-right: 0;
		}
	}
	label:before {
	}
	input:checked ~ label,
	&:not(:checked) > label:hover,
	&:not(:checked) > label:hover ~ label {
		color: $violet4;

		svg {
			stroke: $violet4;
			color: $violet4;
		}
	}
	input:checked + label:hover,
	input:checked ~ label:hover,
	label:hover ~ input:checked ~ label,
	input:checked ~ label:hover ~ label {
		color: $violet4;
		svg {
			color: $violet4;
		}
	}

	@include _mobile {
		&-wrapper {
			justify-content: flex-end;
		}
		&__caption {
			font-size: 14px;
			margin-right: 15px;
		}
		label {
			width: 14px;
			height: 14px;
		}
	}
}
