.order-info {
	padding: 20px 30px;
	background-color: $grey3;
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	flex-wrap: wrap;
	position: sticky;
	top: 180px;
	&__title {
		font-weight: bold;
		font-size: 24px;
		line-height: 30px;
		letter-spacing: 0.115em;
		text-transform: uppercase;
		color: $black;
		width: 100%;
		margin-bottom: 40px;
	}
	&__key {
		width: 50%;
		font-weight: normal;
		font-size: 18px;
		line-height: 23px;
		letter-spacing: 0.115em;
		color: $black;
		margin-bottom: 25px;
	}
	&__value {
		width: 50%;
		font-weight: 700;
		font-size: 18px;
		line-height: 23px;
		text-align: right;
		letter-spacing: 0.115em;
		color: $black;
		margin-bottom: 25px;
	}
	&__href {
		font-weight: bold;
		font-size: 18px;
		line-height: 23px;
		letter-spacing: 0.115em;
		text-decoration-line: underline;
		color: $violet4;
		margin-top: 15px;
		margin-bottom: 50px;
		width: 100%;
		&:hover {
			text-decoration: none;
		}
	}
	.btn-primary {
		padding: 16px 20px;
		width: 100%;
		margin-bottom: 20px;
		margin-top: 35px;
	}
	&__policy {
		display: flex;
		align-items: center;
		font-weight: normal;
		font-size: 12px;
		line-height: 20px;
		color: $grey2;
	}
	.checkbox {
		.checkmark {
			margin-right: 15px;
			width: 16px;
			height: 16px;
		}

		&__caption a {
			display: block;
			color: $violet4;
		}
	}

	@include _mobile-tablet {
		position: static;
		margin-bottom: 40px;
	}

	@include _mobile {
		padding: 20px;
		&__title {
			text-align: center;
		}
		&__key,
		&__value {
			width: 100%;
			text-align: center;
			font-size: 16px;
			line-height: 1;
			&_total {
				text-shadow: $black 0.5px 0px, $black 0.5px 0.5px, $black 0px 0.5px,
					$black -0.5px 0.5px, $black -0.5px 0px, $black -0.5px -0.5px,
					$black 0px -0.5px, $black 0.5px -0.5px;
			}
		}
		&__href {
			text-align: center;
			margin-bottom: 20px;
			margin-top: 0;
		}
		.btn-primary {
			margin-top: 0;
		}
	}

	@include _laptop-sm {
		padding: 20px 10px;
		&__title {
			font-size: 20px;
			line-height: 24px;
			margin-bottom: 20px;
		}

		&__key,
		&__value {
			font-size: 16px;
			line-height: 20px;
		}

		&__href {
			margin-bottom: 30px;
		}

		.btn-primary {
			margin-top: 10px;
			padding: 10px 20px;
		}
	}

	@include _laptop {
		padding: 20px;
		&__key,
		&__value {
			font-size: 18px;
			line-height: 20px;
		}
	}
}
