.catalog-result {
	margin-bottom: $section-margin_pc;
	.product-card {
		margin-bottom: 50px;
	}
	.pagination {
		margin-top: 50px;
	}

	@include _mobile {
		margin-bottom: 100px;
		.product-card {
			margin-right: auto;
			margin-left: auto;
		}
		.pagination {
			margin-top: 20px;
		}
	}
}
