// titles
%title-1 {
	font-size: 48px;
	line-height: 61px;
	font-weight: bold;
	letter-spacing: 0.115em;
	text-transform: uppercase;
	color: $black;
	text-shadow: $black 0.5px 0px, $black 0.5px 0.5px, $black 0px 0.5px,
		$black -0.5px 0.5px, $black -0.5px 0px, $black -0.5px -0.5px,
		$black 0px -0.5px, $black 0.5px -0.5px;
	@include _mobile {
		font-size: 22px;
		line-height: 35px;
	}
	@include _tablet {
		font-size: 28px;
		line-height: 36px;
	}
	@include _laptop-sm {
		font-size: 32px;
		line-height: 38px;
	}
}
%title-2 {
	font-weight: bold;
	font-size: 32px;
	line-height: 41px;
	letter-spacing: 0.115em;
	text-transform: uppercase;
	color: $black;
	text-shadow: $black 0.5px 0px, $black 0.5px 0.5px, $black 0px 0.5px,
		$black -0.5px 0.5px, $black -0.5px 0px, $black -0.5px -0.5px,
		$black 0px -0.5px, $black 0.5px -0.5px;
	@include _mobile {
		font-size: 24px;
		line-height: 30px;
	}

	@include _tablet {
		font-size: 26px;
		line-height: 34px;
	}

	@include _laptop-sm {
		font-size: 28px;
		line-height: 36px;
	}
}
%title-3 {
	font-weight: normal;
	font-size: 28px;
	line-height: 30px;
	color: $black;
}
%title-4 {
	font-weight: bold;
	font-size: 24px;
	line-height: 30px;
	letter-spacing: 0.115em;
	text-transform: uppercase;
	color: $black;

	@include _mobile {
		font-size: 18px;
		line-height: 24px;
	}
}
%title-5 {
	font-weight: bold;
	font-size: 16px;
	line-height: 24px;
	color: $black;
	@include _mobile {
		font-size: 14px;
		line-height: 1;
	}
	@include _laptop-sm {
		font-size: 16px;
		line-height: 20px;
	}
}

%title-6 {
	font-weight: bold;
	font-size: 22px;
	line-height: 24px;
	text-transform: uppercase;
	color: $black;
	@include _mobile {
		font-size: 16px;
		line-height: 20px;
	}
	@include _tablet {
		font-size: 18px;
		line-height: 20px;
	}
	@include _laptop-sm {
		font-size: 20px;
		line-height: 22px;
	}
}
// subtitles

%subtitle-1 {
	display: block;
	font-weight: 300;
	font-size: 24px;
	line-height: 30px;
	letter-spacing: 0.115em;
	text-transform: uppercase;
	color: $black;
	text-decoration: none;
	@include _mobile {
		font-size: 16px;
		line-height: 20px;
	}
	@include _tablet {
		font-size: 18px;
		line-height: 22px;
	}
	@include _laptop-sm {
		font-size: 20px;
		line-height: 26px;
	}
}

%subtitle-2 {
	font-weight: bold;
	font-size: 18px;
	line-height: 26px;
	text-align: center;
	letter-spacing: 0.115em;
	text-transform: uppercase;
	color: $black;
	@include _laptop-sm {
		font-size: 16px;
		line-height: 24px;
	}
}

%subtitle-3 {
	font-weight: normal;
	font-size: 14px;
	line-height: 18px;
	color: $grey1;
}

// text
%text-1 {
	font-weight: 300;
	font-size: 14px;
	line-height: 20px;
	color: $black;
}
%text-2 {
	font-weight: normal;
	font-size: 16px;
	line-height: 24px;
}

%seo-text {
	font-weight: 300;
	font-size: 14px;
	line-height: 18px;
	letter-spacing: 0.115em;
	color: $black;
}

%btn-text1 {
	font-weight: normal;
	font-size: 16px;
	line-height: 28px;
}
%btn-text2 {
	font-weight: normal;
	font-size: 14px;
	line-height: 28px;
}
%input-text {
	font-weight: normal;
	font-size: 16px;
	line-height: 23px;
	letter-spacing: 0.115em;
	color: $black;

	@include _mobile {
		font-size: 14px;
		line-height: 20px;
	}
}
%input-text_sm {
	font-weight: normal;
	font-size: 14px;
	line-height: 1;
	color: $black;
}
// lists
%ul-reset {
	padding: 0;
	margin: 0;
	list-style: none;
}

%ul-check {
	@extend %ul-reset;
	li {
		font-weight: normal;
		font-size: 16px;
		line-height: 18px;
		color: $black;
		position: relative;
		padding-left: 31px;
		margin-bottom: 15px;
		&::before {
			content: '';
			display: block;
			position: absolute;
			width: 16px;
			height: 16px;
			left: 0;
			top: 0;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 100% 100%;
			background-image: url(../img/check-violet.svg);
		}
	}

	@include _mobile {
		li {
			padding-left: 20px;
			&::before {
				width: 12px;
				height: 12px;
				top: 3px;
			}
		}
	}
}

//
%backdrop {
	position: fixed;
	top: 0;
	left: 0;
	background: rgba(79, 82, 80, 0.7);
	backdrop-filter: blur(7px);
}
