.reviews {
	margin-bottom: $section-margin_pc;
	.section-title {
		margin-bottom: 70px;
	}
}

.reviews-card {
	background-color: $grey3;
	margin-bottom: 20px;
	&__heading {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		padding: 15px;
		border-bottom: 1px solid $border-color;
	}
	&__name {
		font-weight: normal;
		font-size: 16px;
		line-height: 26px;
		letter-spacing: 0.115em;
		color: $black;
		text-shadow: $black 0.5px 0px, $black 0.5px 0.5px, $black 0px 0.5px,
			$black -0.5px 0.5px, $black -0.5px 0px, $black -0.5px -0.5px,
			$black 0px -0.5px, $black 0.5px -0.5px;
	}
	&__date {
		margin-left: auto;
		font-weight: 300;
		font-size: 14px;
		line-height: 26px;
		letter-spacing: 0.115em;
		color: $black;
		padding-right: 30px;
		border-right: 1px solid $border-color;
	}
	&__rating {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-left: 30px;
		svg {
			margin-right: 9px;
			width: 18px;
			height: 18px;
		}
	}
	&__text {
		font-weight: bold;
		font-size: 14px;
		line-height: 26px;
		letter-spacing: 0.115em;
		color: $black;
		padding: 20px 140px 20px 15px;
	}
	&_reply {
		margin-left: 120px;
		.reviews-card__date {
			border-right: none;
			padding-right: 0;
		}
	}

	@include _mobile {
		position: relative;
		margin-bottom: 40px;
		&__heading {
			justify-content: space-between;
		}
		&_reply {
			margin-left: 15px;
		}

		&__date {
			top: -26px;
			right: 0;
			position: absolute;
			border: none;
			padding-right: 0;
		}
		&__rating {
			svg {
				width: 14px;
				height: 14px;
				margin-right: 5px;
			}
		}

		&__text {
			padding: 15px;
		}
	}
}
