.reviews-form {
	margin-bottom: $section-margin_pc;
	.section-title {
		margin-bottom: $section-title-margin_pc;
	}
	.input_sm {
		margin-bottom: 20px;
	}
	.input {
		margin-bottom: 30px;
	}
	.rating-wrapper {
		margin-bottom: 20px;
	}
	.btn {
		padding: 10px 71px;
	}

	@include _mobile {
		.section-title {
			margin-bottom: $section-title-margin_mobile;
		}
		.input,
		.input_sm {
			margin-bottom: 20px;
		}

		.btn {
			padding: 10px 40px;
			max-width: 300px;
			width: 100%;
		}
	}
}
