.footer {
	.container {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
	}
	&__title {
		margin-bottom: 20px;
	}

	&__title-text {
		font-weight: bold;
		font-size: 24px;
		line-height: 30px;
		letter-spacing: 0.115em;
		text-transform: uppercase;
		color: $black;
	}

	&__title-toggle {
		opacity: 0;
		visibility: hidden;
		display: none;
	}

	.social {
		margin-bottom: 25px;
		&__item svg {
			color: $black;
		}
	}
	@include _mobile-tablet {
		&__title {
			padding: 15px 0;
			border-bottom: 1px solid $border-color;
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-bottom: 0;
			transition: 0.3s all;
		}
		&__title-text {
			font-size: 18px;
			line-height: 23px;
		}
		&__title-toggle {
			display: flex;
			align-items: center;
			justify-content: center;
			opacity: 1;
			visibility: visible;
			cursor: pointer;
			width: 22px;
			height: 22px;
			position: relative;
			overflow: hidden;
			span {
				background-color: $black;
				position: absolute;
				transform: translate(-50%, -50%);
				top: 50%;
				left: 50%;
				border-radius: 4px;
				transition: 0.3s all;
				&:first-child {
					height: 2px;
					width: 12px;
				}
				&:last-child {
					width: 2px;
					height: 12px;
				}
			}
		}
		&__col {
			width: 100%;
		}
		&__col:last-child {
			.footer__title {
				border-bottom: none;
			}
		}
		.container {
			flex-wrap: wrap;
		}
		.js-footer-wrapper {
			display: none;
			margin-bottom: 20px;
		}

		.active {
			border-bottom: transparent;
		}
		.active-toggle {
			span:last-child {
				transform: translate(-50%, -200%);
			}
		}
	}
}

.footer-menu {
	@extend %ul-reset;
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	max-width: 442px;
	flex-direction: column;
	&_row {
		flex-direction: row;
		flex-wrap: wrap;
		li {
			width: 50%;
		}
		li:nth-child(even) {
			padding-left: 80px;
		}
	}

	li {
		margin-bottom: 15px;
	}
	a {
		font-weight: 300;
		font-size: 14px;
		line-height: 18px;
		letter-spacing: 0.115em;
		color: $black;
		text-decoration: none;
	}

	@include _mobile-tablet {
		max-width: none;
		li {
			width: 100%;
		}
		li:nth-child(even) {
			padding-left: 0;
		}
		a {
			font-weight: 300;
			font-size: 12px;
			line-height: 15px;
		}
	}

	@include _laptop-sm {
		max-width: 230px;
		li {
			width: 100%;
		}
		li:nth-child(even) {
			padding-left: 0;
		}
	}

	@include _laptop {
		max-width: 390px;
	}
}

.footer-contacts {
	a {
		display: block;
		margin-bottom: 25px;
		font-weight: normal;
		font-size: 14px;
		line-height: 18px;
		letter-spacing: 0.115em;
		text-decoration: none;
		color: $black;
	}
}

.footer-copyright {
	padding: 25px 0 35px 0;
	border-top: 1px solid #e5e5e5;
	margin-top: 66px;
	span {
		font-weight: 300;
		font-size: 12px;
		line-height: 15px;
		letter-spacing: 0.115em;
		color: $grey2;
		br {
			display: none;
		}
	}

	@include _mobile-tablet {
		margin-top: 0;
		span {
			width: 50%;
			br {
				display: block;
			}
			&:last-child {
				text-align: right;
			}
		}
	}
}
