.text-section {
	margin-bottom: 100px;
	&_last {
		margin-bottom: $section-margin_pc;
	}
	&_md {
		margin-bottom: 70px;
	}
	&_sm {
		margin-bottom: 40px;
	}
	.title {
		&_h3 {
			margin-bottom: 40px;
		}
		&_h5 {
			margin-bottom: 30px;
		}
	}
	.section-title {
		margin-bottom: 70px;
	}
	p {
		@extend %text-2;
		margin-bottom: 25px;
		margin-top: 0;
		&:last-child {
			margin-bottom: 0;
		}
	}
	.ul-check {
		margin-top: 30px;
	}
	&__img {
		&_big {
			width: 100%;
		}
		&_date {
			position: relative;
			.date {
				position: absolute;
				top: 0;
				left: 0;
				padding: 12px 18px;
				background-color: $black;
				font-weight: bold;
				font-size: 16px;
				line-height: 18px;
				color: #ffffff;
			}
		}
	}

	@include _mobile-tablet {
		&__img {
			width: fit-content;
			margin-left: auto;
			margin-right: auto;
			margin-top: 25px;
		}
		.ul-check {
			li {
				line-height: 24px;
			}
		}
	}
	@include _mobile {
		.title {
			&_h3 {
				margin-bottom: 20px;
			}
			&_h5 {
				font-size: 18px;
			}
		}
		&_md {
			margin-bottom: 40px;
		}
		.section-title {
			word-break: break-all;
			font-size: 20px;
			margin-bottom: 20px;
		}
	}
}
