.therapy-advantages {
	margin-bottom: calc(#{$section-margin_pc} + 50px);
	&__wrapper {
		background-color: $black;
		padding: 47px 112px 47px 40px;
		max-width: 1130px;
	}
	&__img {
		position: absolute;
		right: 0;
		bottom: -50px;
		width: 327px;
		z-index: 2;
		height: 550px;
	}
	p,
	.title_h3 {
		color: #fff;
	}
	.title_h3 {
		margin-bottom: 30px;
	}
	p {
		margin-top: 0;
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	.container {
		display: flex;
		align-items: flex-end;
		justify-content: flex-start;
		position: relative;
	}

	@include _mobile-tablet {
		margin-bottom: #{$section-margin_mobile + 50px};
		&__wrapper {
			padding: 30px 20px;
		}

		.container {
			flex-wrap: wrap;
		}
	}

	@include _mobile {
		&__img {
			margin-left: auto;
			margin-right: auto;
			position: static;
			height: auto;
		}
	}
	@include _tablet {
		&__wrapper {
			padding-left: 240px;
		}
		&__img {
			left: 0;
			// top: 35px;
			top: 10px;
			width: fit-content;
			height: 380px;
			img {
				height: 100%;
			}
		}
	}

	@include _laptop {
		&__wrapper {
			padding-right: 290px;
		}
	}

	@include _laptop-sm {
		&__wrapper {
			padding-right: 330px;
		}
	}
}
