.pagination {
	@extend %ul-reset;
	display: flex;
	align-items: center;
	justify-content: center;

	&__item {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 36px;
		height: 36px;
		transition: 0.3s all;
		svg {
			width: 18px;
			height: 18px;
		}
		&_prev {
			margin-right: 30px;
			svg {
				transform: rotate(180deg);
			}
		}
		&_next {
			margin-left: 30px;
		}
		&_prev,
		&_next {
			border: 1px solid $grey2;
		}
		a {
			display: flex;
			width: 100%;
			height: 100%;
			align-items: center;
			justify-content: center;
			font-weight: bold;
			font-size: 16px;
			line-height: 20px;
			letter-spacing: 0.115em;
			color: $black;
			text-decoration: none;
			transition: 0.3s all;
		}

		&_active,
		&:hover {
			background-color: $violet2;
			a {
				color: #fff;
			}
		}
	}

	@include _mobile {
		&__item {
			&_prev {
				margin-right: 10px;
			}
			&_next {
				margin-left: 10px;
			}
		}
	}
}
