.carousel {
	margin-bottom: $section-margin_pc;
	.product-card {
		margin-bottom: 0;
	}
	.container {
		position: relative;
	}
	.section-title {
		margin-bottom: 70px;
	}
	&__item {
	}
	&__next-btn {
		position: absolute;
		padding: 8px 50px 8px 20px;
		background-color: $violet4;
		border: none;
		cursor: pointer;
		top: 60%;
		z-index: 2;
		right: -50px;
		svg {
			color: #fff;
			width: 20px;
			height: 20px;
			transition: 0.3s all;
		}
		&:hover {
			svg {
				transform: translateX(5px);
			}
		}
	}
	@include _mobile-tablet {
		.section-title {
			margin-bottom: $section-title-margin_mobile;
		}
		&__next-btn {
			opacity: 0;
			visibility: hidden;
			display: none;
		}

		.product-card {
			margin-left: auto;
			margin-right: auto;
		}
	}
	@media (min-width: 1024px) {
		margin-bottom: 100px;
		.section-title {
			margin-bottom: 50px;
		}
		&__next-btn {
			right: 0;
		}
	}
}
