.blog {
	margin-bottom: $section-margin_pc;
	.pagination {
		margin-top: 70px;
	}
}

.blog-card {
	margin-bottom: 30px;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	min-height: 410px;
	position: relative;
	z-index: 1;
	color: #fff;
	padding: 50px 20px;
	display: flex;
	align-items: flex-end;
	justify-content: flex-start;
	&:after {
		content: '';
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		background-color: rgba(79, 82, 80, 0.6);
	}
	&_no-overlay {
		&:after {
			content: none;
		}
	}
	&_sm {
		min-height: 190px;
		padding: 20px 30px;
	}
	&__date {
		position: absolute;
		padding: 13px 18px;
		background-color: $black;
		top: 0;
		left: 0;
		&_white {
			background-color: #fff;
			color: $black;
		}
	}
	&__title {
		font-weight: bold;
		font-size: 20px;
		line-height: 28px;
		text-transform: uppercase;
		color: #ffffff;
		text-decoration: none;
		&_sm {
			max-width: 290px;
			font-weight: bold;
			font-size: 16px;
			line-height: 26px;
			text-transform: uppercase;
			color: #ffffff;
		}
	}
	.btn-arrow {
		position: absolute;
		bottom: 0;
		right: -16px;
		padding: 8px 43px 8px 20px;
	}
	&:hover {
		cursor: pointer;
		text-decoration: underline;
	}

	@include _mobile-tablet {
		.btn-arrow {
			right: 0;
		}
	}
	@include _mobile {
		min-height: 300px;
		padding-bottom: 50px;
		&_sm {
			min-height: 300px;
		}
		&__title {
			font-size: 16px;
			line-height: 20px;
		}
	}

	@include _tablet {
		min-height: 300px;
		&_sm {
			min-height: 300px;
		}
	}
}
