.hero-breadcrumbs {
	padding: 24px 30px;
	background-color: #fbfbfb;
	margin-bottom: $section-margin_pc;
	&__title {
		margin-top: 0;
		margin-bottom: 15px;
		text-align: center;
	}

	@include _mobile {
		padding: 15px 13px;
		margin-bottom: 30px;
		&__title {
			font-size: 20px;
			line-height: 25px;
		}
	}
	@include _laptop-sm {
		margin-bottom: 50px;
		&__title {
			font-size: 28px;
		}
	}
	@include _laptop-sm {
		margin-bottom: 35px;
	}
	@include _laptop {
		margin-bottom: 35px;
	}

	@include _large {
		margin-bottom: 35px;
	}
}
