.checkbox {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	cursor: pointer;
	input {
		opacity: 0;
		visibility: hidden;
		display: none;
	}
	.checkmark {
		height: 20px;
		width: 20px;
		border: 1px solid $border-color;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		&::after {
			content: '';
			opacity: 0;
			visibility: hidden;
			width: 16px;
			position: absolute;
			height: 16px;
			display: flex;
			align-items: center;
			justify-content: center;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 100% 100%;
			background-image: url(../img/check-2.svg);
		}
	}

	input:checked ~ .checkmark {
		&:after {
			opacity: 1;
			visibility: visible;
		}
	}
	input:checked ~ .checkbox__text {
		color: $violet4;
		font-weight: bold;
	}

	@include _mobile {
		.checkbox__text {
			font-size: 14px;
			line-height: 1;
		}
	}
}

.input {
	width: 100%;
	background-color: #fff;
	position: relative;
	input,
	textarea {
		width: 100%;
		@extend %input-text;
		padding: 18px;
		background-color: transparent;
		outline: none;
		border: 1px solid $border-color;
		resize: none;
	}
	&_grey {
		input {
			border: none;
			background-color: $border-color2;
		}
	}
	&_sm {
		input {
			@extend %input-text_sm;
		}
	}
	&__placeholder {
		position: absolute;
		z-index: 1;
		cursor: text;
		top: 20px;
		left: 19px;
		width: auto;
		color: #b3b3b3;
		pointer-events: none;
		font-weight: normal;
		font-size: 14px;
		line-height: 14px;
		color: $black;
		&::after {
			content: ' *';
			font-size: 16px;
			color: $red;
		}
	}
	&__message {
		display: block;
		// height: 0;

		@extend %input-text_sm;
		color: $red;
		opacity: 0;
		visibility: hidden;
		max-height: 0;
		transition: 0.3s all;
	}
	&_error {
		input {
			border-color: $red;
			background-color: rgba(153, 1, 100, 0.1);
		}
		.input__message {
			opacity: 1;
			visibility: visible;
			max-height: 400px;
			margin-top: 10px;
		}
	}
	@include _mobile {
		input,
		textarea {
			padding: 14px 16px;
		}

		&__placeholder {
			top: 16px;
		}
	}

	@include _tablet {
		input {
			padding: 14px;
		}
	}
}

.ss-main {
	font-weight: bold;
	font-size: 16px;
	line-height: 24px;
	letter-spacing: 0.115em;
	color: $black;
	.ss-single-selected {
		padding: 8px 15px;
		height: auto;
		background: #ffffff;
		border: 1px solid $border-color;
		border-radius: 0;
		.placeholder {
			font-weight: bold;
			font-size: 16px;
			line-height: 24px;
			letter-spacing: 0.115em;
			color: #4f5250;
		}
		.ss-arrow {
			.arrow-down,
			.arrow-up {
				width: 16px;
				height: 16px;
				background-repeat: no-repeat;
				background-position: center center;
				background-size: 100% 100%;
				background-image: url(../img/dropdown-arrow.svg);
				border-width: 0;
				border: none;
			}
			span.arrow-down {
				transform: rotate(0deg);
				margin: 0;
			}
			span.arrow-up {
				transform: rotate(180deg);
				margin: 0;
			}
		}
	}
}

.ss-content {
	z-index: 5;
	.ss-list {
		&::-webkit-scrollbar-track {
			background-color: #e5e5e5;
		}
		&::-webkit-scrollbar-thumb {
			border-radius: 5px;
			background-color: $violet4;
		}
		&::-webkit-scrollbar {
			border-radius: 5px;
			width: 5px;
		}
	}
}
