.hero {
	position: relative;
	z-index: 1;
	margin-bottom: $section-margin_pc;
	&__carousel-navigation {
		margin-left: auto;
		width: fit-content;
		svg {
			color: $violet4;
		}
	}
	.contianer {
		height: 100%;
	}

	.owl-item img {
		height: 100% !important;
	}
	@include _mobile-tablet {
		&__carousel-navigation {
			display: none;
			opacity: 0;
			visibility: hidden;
		}
	}
	@include _mobile {
		margin-bottom: 30px;
		.btn-border-pink {
			opacity: 1;
			visibility: visible;
			padding: 6px 20px;
			margin-top: 30px;
			max-width: 300px;
			margin-left: auto;
			margin-right: auto;
		}
	}

	@media (min-width: 768px) {
		.btn-border-pink {
			opacity: 0;
			visibility: hidden;
			display: none;
		}
	}
}

.hero-item {
	min-height: 650px;
	&__text {
		font-weight: 300;
		font-size: 16px;
		line-height: 22px;
		color: $black;
		transition: 0.3s all;
	}
	&__img {
		position: absolute;
		z-index: -1;
		right: 0;
		top: 0;
	}
	@include _mobile {
		min-height: 325px;
	}
	@include _tablet {
		min-height: 410px;
	}
	@include _laptop-sm {
		&__text {
			line-height: 20px;
		}
	}
}

.hero-sales {
	height: 575px;
	display: flex;
	align-items: flex-end;
	justify-content: flex-end;
	text-decoration: none;
	&__text {
		padding: 12px 5px 12px 17px;
		background-color: #fff;
		margin-bottom: 40px;
		transition: 0.3s all;
	}
	&__title {
		font-weight: bold;
		font-size: 22px;
		line-height: 28px;
		color: $violet4;
		margin-bottom: 12px;
	}
	&:hover {
		.hero-sales__text {
			background-color: $black;
			.hero-item__text {
				color: #fff;
			}
		}
	}

	@include _mobile-tablet {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
}

.hero-main {
	&__text {
		margin-bottom: 100px;
		margin-top: 180px;
		h1 {
			margin-bottom: 20px;
			margin-top: 0;
		}
		.subtitle-1 {
			margin-bottom: 40px;
		}
		.btn {
			padding: 10px 59px;
			width: fit-content;
		}
	}

	@include _mobile {
		&__text {
			margin-top: 70px;
			margin-bottom: 0;
			h1 {
				width: 80%;
				margin-bottom: 15px;
			}
			.subtitle-1 {
				margin-bottom: 30px;
			}
		}
	}

	@include _tablet {
		&__text {
			margin-top: 80px;
			h1 {
				width: 60%;
			}
			.subtitle-1 {
				margin-bottom: 30px;
			}
		}
	}
	@include _laptop-sm {
		&__text {
			margin-bottom: 150px;
		}
		.subtitle-1 {
			margin-bottom: 10px;
		}
	}
	@include _laptop {
		&__text {
			margin-bottom: 40px;
		}
	}
}

.hero-notice {
	padding: 12px 130px 12px 20px;
	background-color: #fff;
	max-width: 450px;
	margin-left: 240px;
	&__text {
		line-height: 28px;
	}

	@include _mobile-tablet {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
}
