.product-card {
	cursor: pointer;
	margin-bottom: 40px;
	&_not-available {
		opacity: 0.5;
		transition: 0.3s all;
		&:hover,
		&:focus {
			opacity: 0.8;
		}
	}
	&__img {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 20px;
		img {
			width: 100%;
			height: 100%;
		}
		&::after {
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background-color: rgba(153, 16, 155, 0.7);
			backdrop-filter: blur(4px);
			opacity: 0;
			visibility: hidden;
			pointer-events: none;
			transition: 0.3s all;
		}
	}
	.stickers {
		position: absolute;
		z-index: 2;
		top: 10px;
		left: 20px;
		&_not-available {
			top: -10px;
			left: 0;
		}
	}
	&__title {
		display: block;
		font-weight: normal;
		font-size: 14px;
		line-height: 24px;
		text-align: center;
		letter-spacing: 0.115em;
		text-transform: uppercase;
		color: $black;
		margin-bottom: 20px;
		max-height: 48px;
		overflow: hidden;
		text-decoration: none;
	}
	&__price {
		font-weight: bold;
		font-size: 20px;
		line-height: 24px;
		text-align: center;
		letter-spacing: 0.115em;
		color: #000000;
	}
	.btn {
		padding: 10px 55px;
		position: absolute;
		z-index: 2;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		transition: 0.3s all;
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
		overflow: hidden;
		&::after {
			content: '';
			width: 100%;
			height: 100%;
			position: absolute;
			background-color: $black;
			z-index: -1;
			transform: translateX(-100%);
			transition: 0.3s all;
		}
		&:hover,
		&:focus {
			color: #fff;
			&::after {
				transform: translateX(0);
			}
		}
	}
	.btn-like {
		position: absolute;
		z-index: 2;
		right: 10px;
		top: 10px;
		width: 48px;
		height: 48px;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 14px 13px;
		border-radius: 50%;
		transition: 0.3s all;
		svg {
			color: $black;
			transition: 0.3s all;
		}
		&:hover,
		&:focus {
			svg {
				color: $violet4;
				fill: $violet4;
			}
		}
	}
	&__delete {
		width: 22px;
		height: 22px;
		display: flex;
		align-items: center;
		justify-content: center;
		position: absolute;
		top: 25px;
		right: 25px;
		padding: 0;
		border: none;
		z-index: 2;
		background-color: transparent;
		cursor: pointer;
		svg {
			width: 100%;
			height: 100%;
			color: #9e9e9e;
		}
	}

	@include _mobile-tablet {
		max-width: 380px;
	}

	// @include _tablet {
	// 	max-width: 400px;
	// }

	@media (min-width: 1024px) {
		&:hover,
		&:focus {
			.product-card__img::after {
				opacity: 1;
				visibility: visible;
			}
			.btn-primary {
				opacity: 1;
				visibility: visible;
				pointer-events: inherit;
			}
			.btn-like {
				background-color: #fff;
			}
		}
	}
}
