.cart {
	margin-bottom: $section-margin_pc;
	&__section {
		margin-bottom: 50px;
		&:last-child {
			margin-bottom: 70px;
		}
	}
	&__back {
		font-weight: bold;
		font-size: 16px;
		line-height: 18px;
		text-transform: uppercase;
		color: $black;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		text-decoration: none;
		svg {
			color: $black;
			width: 20px;
			height: 20px;
			transform: rotate(90deg);
			transition: 0.3s all;
		}
		&:hover {
			svg {
				transform: rotate(90deg) translateY(5px);
			}
		}
	}

	.ss-main {
		.ss-single-selected {
			padding: 18px;
			.placeholder {
				@extend %input-text_sm;
			}
		}
	}
	.select {
		margin-bottom: 25px;
	}

	.title_h5 {
		padding: 22px 16px;
		background-color: $grey3;
		width: 100%;
		text-transform: uppercase;
		margin-bottom: 30px;
	}
	.input {
		margin-bottom: 25px;
	}
	.checkbox {
		margin-bottom: 30px;
		width: fit-content;
		.checkmark {
			margin-right: 15px;
		}
		&_margin {
			margin-bottom: 15px;
		}
	}
	@include _mobile {
		margin-bottom: 50px;
		.title_h5 {
			padding: 14px 16px;
			margin-bottom: 15px;
		}
		&__section {
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 20px;
			}
		}
		.ss-main {
			.ss-single-selected {
				padding: 14px;
			}
		}

		.checkbox {
			margin-bottom: 15px;
		}
	}

	@include _tablet {
		margin-bottom: 70px;
	}
	@include _laptop-sm {
		&__section {
			margin-bottom: 30px;
		}
		.title_h5 {
			padding: 18px 16px;
		}
		.checkbox {
			margin-bottom: 20px;
		}
	}
}
