.catalog-category-card {
	background-repeat: no-repeat;
	background-position: center center;
	background-position: center center;
	background-size: 100% 100%;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	min-height: 120px;
	padding: 10px;
	text-decoration: none;
	margin-bottom: 70px;
	transition: 0.3s all;
	&__title {
		font-weight: bold;
		font-size: 16px;
		line-height: 18px;
		color: #000000;
		padding: 6px;
		transition: 0.3s all;
		position: relative;
		z-index: 1;
		overflow: hidden;
		&::after {
			opacity: 0;
			visibility: hidden;
			content: '';
			width: 100%;
			height: 100%;
			background-color: $black;
			position: absolute;
			top: 0;
			left: 0;
			z-index: -1;
			transition: 0.3s all;
			transform: translateX(-100%);
		}
	}

	@include _mobile {
		margin-bottom: 20px;
		min-height: 100px;
		background-size: cover;
		&__title {
			font-size: 14px;
			line-height: 16px;
		}
	}
	@include _tablet {
		background-size: cover;
		min-height: 100px;
		max-width: 400px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 20px;
	}
	@include _laptop-sm {
		min-height: 80px;
		margin-bottom: 30px;
		// background-size: cover;
	}
	@media (min-width: 1024px) {
		&:hover {
			background-size: 120% 120%;
			.catalog-category-card__title {
				color: #fff;
				&::after {
					transform: translateX(0);
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}
}

.catalog-heading {
	.filter__btn {
		width: 100%;
		padding: 14px 80px;
		border: 1px solid $border-color;
		margin-bottom: 15px;
		max-width: 400px;
		// margin-left: auto;
		margin-right: auto;
	}
	.sort {
		width: fit-content;
		margin-bottom: 40px;
	}
}

.catalog-category {
	margin-bottom: 20px;
}
