.header-categories {
	@extend %ul-reset;
	display: flex;
	align-items: center;
	justify-content: space-between;
	li {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		position: relative;
	}
	a {
		font-weight: normal;
		font-size: 16px;
		line-height: 18px;
		color: #000000;
		text-decoration: none;
		transition: 0.3s all;
	}
	&__toggle {
		width: 18px;
		height: 18px;
		margin-left: 5px;
		transition: 0.3s all;
		svg {
			width: 100%;
			height: 100%;
		}
	}

	@include _mobile-tablet {
		flex-direction: column;
		align-items: flex-start;
		padding-bottom: 20px;
		border-bottom: 1px solid $border-color;
		margin-bottom: 30px;
		width: 100%;
		align-items: flex-start;
		justify-content: flex-start;
		a {
			font-size: 14px;
			line-height: 16px;
			padding: 10px 0;
		}
		li {
			flex-wrap: wrap;
			width: 100%;
		}
		.active {
			transform: rotate(180deg);
		}
	}

	@media (min-width: 1024px) {
		li:hover,
		li:focus {
			a {
				color: $violet4;
			}
			.header-categories__toggle {
				transform: rotate(180deg);
			}
			.header-categories-menu {
				opacity: 1;
				visibility: visible;
				pointer-events: inherit;
				a {
					color: $black;
				}
			}
		}
	}
}

.header-categories-menu {
	@extend %ul-reset;
	margin-top: 10px;
	padding: 30px 20px;
	position: absolute;
	opacity: 0;
	z-index: 6;
	visibility: hidden;
	pointer-events: none;
	left: 0;
	top: 100%;
	background-color: #fff;
	box-shadow: 0px 3px 6px rgba(79, 82, 80, 0.1);
	min-width: 180px;
	&::after {
		content: '';
		width: 100%;
		padding-top: 10px;
		position: absolute;
		top: -10px;
		left: 0;
	}
	li {
		display: block;
		width: 100%;
		border-bottom: 1px solid $border-color;
		margin-bottom: 10px;
	}
	li:last-child {
		border-bottom: none;
	}
	a {
		display: block;
		font-weight: normal;
		font-size: 14px;
		line-height: 1.27;
		letter-spacing: 0.115em;
		color: $black;
		padding-bottom: 10px;
		position: relative;
		overflow: hidden;
		transition: 0.3s all;
		&::after {
			content: '';
			width: 100%;
			height: 100%;
			position: absolute;
			background-color: $black;
			top: -10px;
			left: 0;
			opacity: 0;
			visibility: hidden;
			transition: 0.3s all;
			transform: translateX(-100%);
			z-index: -1;
		}
		&:hover,
		&:focus {
			color: #fff !important;
			&::after {
				transform: translateX(0);
				opacity: 1;
				visibility: visible;
			}
		}
	}

	@include _mobile-tablet {
		position: relative;
		display: none;
		width: 100%;
		opacity: 1;
		visibility: visible;
		box-shadow: none;
		padding-top: 0;
		padding-left: 0;
		padding-bottom: 0;
		margin-top: 0;
		&::after {
			content: none;
		}
		a {
			font-size: 12px;
		}
	}
	@media (min-width: 1024px) {
		transition: 0.3s all;
	}
}
