.stickers {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	&__item {
		width: 48px;
		height: 48px;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		font-weight: 300;
		font-size: 14px;
		line-height: 24px;
		text-align: center;
		letter-spacing: 0.115em;
		&_violet {
			background-color: $violet4;
			color: #fff;
		}
		&_square {
			border-radius: 0;
			background-color: $black;
			padding: 2px 25px;
			font-weight: normal;
			font-size: 12px;
			line-height: 24px;
			text-align: center;
			letter-spacing: 0.115em;
			color: #ffffff;
			width: auto;
		}
		&_big {
			width: 60px;
			height: 60px;
			font-size: 18px;
		}
	}

	@include _mobile {
		&__item {
			font-size: 12px;
			line-height: 1;
			&_big {
				width: 40px;
				height: 40px;
			}
		}
	}
}
