.breadcrumbs {
	@extend %ul-reset;
	display: flex;
	align-items: center;
	justify-content: center;
	li {
		font-weight: normal;
		font-size: 14px;
		line-height: 16px;
		text-align: center;
		color: $black;
	}
	li a {
		color: $black;
		text-decoration: none;
		transition: 0.3s all;
		&:hover,
		&:focus {
			color: $violet4;
		}
	}
	.slash {
		margin: 0 5px;
	}

	@include _mobile {
		width: 100%;
		flex-wrap: wrap;
		li {
			font-size: 10px;
			line-height: 14px;
			min-width: fit-content;
			width: min-content;
			word-break: break-all;
		}
	}
}
