.popular {
	margin-bottom: $section-margin_pc;
	.section-title {
		margin-bottom: $section-title-margin_pc;
	}
	.product-card {
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 30px;
	}
	@include _mobile {
		margin-bottom: #{$section-margin_mobile + 30px};

		.section-title {
			margin-bottom: $section-title-margin_mobile;
		}
		.btn-load-more {
			margin-top: 30px;
		}
	}
}
