.callback {
	margin-bottom: $section-margin_pc;
	.section-title {
		max-width: 610px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 20px;
	}
	.subtitle-2 {
		max-width: 480px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 60px;
	}
	&__form {
		.btn {
			padding: 15.5px 30px;
			width: 100%;
		}
	}

	@include _mobile {
		margin-bottom: 100px;
		&__form {
			.input {
				margin-bottom: 20px;
			}
		}
	}

	@include _tablet {
		margin-bottom: 100px;
		.subtitle-2 {
			margin-bottom: 40px;
		}

		&__form {
			.btn {
				margin-top: 20px;
				padding: 10.5px 30px;
			}
		}
	}

	@include _laptop-sm {
		bottom: 100px;

		.subtitle-2 {
			margin-bottom: 40px;
		}
	}
}
