.sort {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	margin-left: auto;
	position: relative;
	z-index: 5;
	&__caption {
		font-weight: bold;
		font-size: 14px;
		line-height: 18px;
		letter-spacing: 0.115em;
		color: $grey2;
		margin-right: 20px;
	}
	&__toggle {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		padding-bottom: 5px;
		border-bottom: 1px solid $black;
		cursor: pointer;
		svg {
			width: 18px;
			height: 18px;
			color: $black;
			transition: 0.3s all;
		}
		&_active {
			svg {
				transform: rotate(180deg);
			}
		}
	}

	@include _mobile {
		&__caption {
			font-size: 12px;
			line-height: 15px;
			margin-right: 10px;
		}
		&__toggle {
			font-size: 12px;
			line-height: 15px;
			padding-bottom: 0;
		}
	}
}

.sort-menu {
	position: absolute;
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	flex-direction: column;
	background-color: #fff;
	padding: 10px 20px;
	box-shadow: 0px 3px 6px rgba(79, 82, 80, 0.1);
	right: 0;
	top: 100%;
	opacity: 0;
	visibility: hidden;
	transition: 0.3s all;
	&__link {
		display: block;
		width: 100%;
		text-align: right;
		padding-bottom: 10px;
		text-decoration: none;
		color: $black;
		transition: 0.3s all;
		&:hover {
			color: $violet4;
		}
	}
	&_active {
		opacity: 1;
		visibility: visible;
	}

	@include _mobile {
		&__link {
			font-size: 12px;
			line-height: 15px;
		}
	}
}
