.therapy-section {
	margin-bottom: $section-margin_pc;
	.title_h2 {
		margin-bottom: 30px;
	}
	&__text {
		&_sm {
			margin-bottom: 50px;
			max-width: 570px;
		}
		.href {
			font-weight: normal;
			font-size: 16px;
			line-height: 28px;
			color: $violet4;
			width: fit-content;
			&:hover {
				text-decoration: none;
			}
		}
		p {
			margin-top: 0;
			margin-bottom: 20px;
		}
	}
	&__img {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		width: fit-content;
		&_right {
			margin-left: auto;
			margin-bottom: 50px;
		}
	}

	@include _mobile-tablet {
		margin-bottom: 100px;
		&__text {
			display: flex;
			flex-direction: column;
			order: 1;
			margin-bottom: 45px;
		}
		&__img {
			margin-bottom: 60px;
			margin-left: auto;
			margin-right: auto;
			&_last {
				margin-bottom: 0;
			}
		}
		&_reverse-flex {
			display: flex;
			flex-direction: column-reverse;
		}
	}
}
