.product-page-gallery {
	margin-bottom: $section-margin_pc;
	&__rating {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		margin-bottom: 20px;
		svg {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 20px;
			height: 20px;
			margin-right: 15px;
			&:last-child {
				margin-right: 0;
			}
		}
		a {
			font-weight: bold;
			font-size: 16px;
			line-height: 24px;
			letter-spacing: 0.115em;
			color: $grey2;
			text-decoration: none;
			transition: 0.3s all;
			&:hover {
				color: $violet4;
			}
		}
	}
	&__big {
		position: relative;
		border: 1px solid #e5e5e5;
		margin-bottom: 20px;
		cursor: pointer;
	}
	&__thumbs {
	}

	@include _mobile {
		margin-bottom: 15px;
		&__rating {
			align-items: center;
			margin-bottom: 15px;
			svg {
				width: 14px;
				height: 14px;
				margin-right: 10px;
			}
			a {
				font-size: 12px;
				line-height: 24px;
			}
		}
	}

	@include _tablet {
		&__rating {
			margin-left: auto;
			margin-right: auto;
			width: fit-content;
		}
	}
	@include _tablet {
		margin-bottom: 100px;
	}
	@media (min-width: 1430px) {
		position: sticky;
		top: 190px;
		&__wrapper {
		}
	}
}

.big-gallery {
	max-width: 564px;
	.stickers {
		position: absolute;
		top: 20px;
		left: 20px;
		z-index: 2;
	}
	.btn-like {
		position: absolute;
		top: 25px;
		right: 25px;
		z-index: 2;
		padding: 0;
		width: 26px;
		height: 26px;
	}
	&__img {
		display: flex;
		align-items: center;
		justify-content: center;
		overflow: hidden;
		width: 100%;
		height: auto;
	}
	.owl-nav {
		position: absolute;
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 0;
		left: 15px;
		top: 50%;
		width: calc(100% - 30px);
		button {
			width: 22px;
			height: 22px;
			svg {
				color: $border-color;
				transition: 0.3s all;
			}
			&:hover {
				svg {
					color: $violet4;
				}
			}
		}
	}
	@include _mobile {
		margin-left: auto;
		margin-right: auto;
		.stickers {
			top: 10px;
			left: 10px;
		}

		.btn-like {
			top: 15px;
			right: 15px;
		}
	}
	@include _tablet {
		max-width: 600px;
		margin-left: auto;
		margin-right: auto;
	}
}

.thumbs-gallery {
	&__img {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 130px;
		height: 130px;
		border: 1px solid $border-color2;
		transition: 0.3s all;
		cursor: pointer;
		overflow: hidden;
	}
	.current {
		.thumbs-gallery__img {
			border-color: $violet4;
		}
	}

	@include _mobile {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
	@include _tablet {
		max-width: 600px;
		margin-left: auto;
		margin-right: auto;
	}

	@include _laptop-sm {
		&__img {
			width: 110px;
			height: 110px;
		}
	}
}
