.e404 {
	margin-top: $section-margin_pc;
	margin-bottom: $section-margin_pc;
	&__title {
		font-family: $secondary-font;
		font-weight: normal;
		font-size: 300px;
		line-height: 300px;
		text-align: center;
		letter-spacing: 0.115em;
		text-transform: uppercase;
		color: $black;
		margin-bottom: 15px;
	}
	&__subtitle {
		margin-bottom: 20px;
		font-weight: bold;
		font-size: 50px;
		line-height: 56px;
		color: $black;
		text-align: center;
	}
	&__text {
		max-width: 830px;
		margin-left: auto;
		margin-right: auto;
		display: block;
		font-weight: bold;
		font-size: 16px;
		line-height: 22px;
		text-align: center;
		color: $black;
		margin-bottom: 50px;
	}
	.btn {
		padding: 16px 111px;
		width: fit-content;
		margin-left: auto;
		margin-right: auto;
	}

	@include _mobile {
		&__title {
			font-size: 70px;
			line-height: 1;
		}
		&__subtitle {
			font-size: 30px;
			line-height: 1;
		}
		&__text {
			font-size: 14px;
			line-height: 20px;
		}

		.btn {
			padding: 10px 20px;
		}
	}
}
