.about {
	margin-bottom: $section-margin_pc;
	&__section {
		margin-bottom: 130px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__text {
		&_right {
			padding-left: 40px;
		}
		&_left {
			max-width: 860px;
		}
	}
	p {
		margin-top: 0;
		margin-bottom: 25px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	.title {
		&_h2 {
			margin-bottom: 40px;
		}
		&_h4 {
			margin-bottom: 40px;
		}
	}
	.subtitle-3 {
		margin-bottom: 20px;
	}
	@include _mobile-tablet {
		&__text {
			&_right {
				padding-left: 0;
			}
		}
		&__section {
			margin-bottom: 70px;
		}
		&__img {
			margin-top: 20px;
			width: fit-content;
			margin-left: auto;
			margin-right: auto;
		}

		.title {
			&_h4 {
				margin-bottom: 20px;
			}
		}
	}
}

.about-sticker {
	margin-top: 70px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 50px 25px;
	// height: 100%;
	&_white {
		background-color: #eeeeee;
		color: $violet4;
	}
	&_violet {
		background-color: rgba(153, 16, 155, 0.7);
		color: #fff;
	}
	&__number {
		font-style: normal;
		font-weight: bold;
		font-size: 70px;
		line-height: 70px;
		margin-bottom: 20px;
	}
	span {
		font-weight: bold;
		font-size: 18px;
		line-height: 26px;
		text-transform: uppercase;
	}

	@include _mobile {
		margin-top: 20px;
	}

	@include _tablet {
		max-width: 400px;
		margin-left: auto;
		margin-right: auto;
		margin-top: 40px;
	}

	@include _laptop-sm {
		&__number {
			font-size: 50px;
			line-height: 1;
		}
		span {
			font-size: 16px;
			text-align: center;
		}
	}

	@include _laptop {
		&__number {
			font-size: 55px;
			line-height: 1;
		}
		span {
			text-align: center;
		}
	}
}
